<template>
<div class="user-profile-sidebar" style="display: block;"> 
    <div class="search-message-sidebar info-bar " style="display: block;">
        <div class="px-3 px-lg-3 pt-3 pt-lg-3 pb-4 mb-4 border-bottom">
            <div class="user-chat-nav d-flex justify-content-between align-items-center">
                <h5 class="mb-0">Message info</h5>
                <button type="button" class="btn nav-btn" id="search-message-hide">
                    <i class="ri-close-line" v-on:click="$parent.showMessageInfo = false"></i>
                </button>
            </div>
        </div>
       
        <div id="search-forward-error" class="text-center text-danger border p-2" v-if="EditMessagInfoListData.length == 0">{{
                        $t('NoRecordfound') }}</div>
        <!-- End profile user -->
        <!-- Start user-profile-desc -->

        <div class="p-3 user-profile-desc pt-0" data-simplebar="init" >
            <div class="simplebar-wrapper" style="margin: 0px -16px -16px;">
                <div class="simplebar-height-auto-observer-wrapper">
                    <div class="simplebar-height-auto-observer"></div>
                </div>
                <div class="simplebar-mask" v-if="!EditMessagInfoListData[0].IS_GROUP">
                    <div class="simplebar-offset" style="right: 0px; bottom: 0px;">
                        <div class="simplebar-content-wrapper"
                            style="height: 100%; padding-right: 0px; padding-bottom: 0px; overflow: hidden;">
                            <div class="simplebar-content" style="padding: 0px 16px 16px;"
                                v-for="msgitem,inedx in EditMessagInfoListData" :key="inedx">
                                <template v-if="msgitem.TO_USER_ID!=store.getUserInformation.ID">
                                <div class="text-muted border-bottom pb-4 mb-3 sm-desc">
                                    <div class="ctext-wrap">
                                        <div class="ctext-wrap-content">
                                            <div class="d-flex justify-content-between">
                                                <p class="mb-0">
                                                    {{ msgitem.MESSAGE }}
                                                </p>
                                                <p class="chat-time mb-0 d-flex align-items-center"> <span
                                                        class="align-middle me-1">{{ getPreviousDay(msgitem.CREATED_AT)
                                                        }}</span> <span class="read "><i
                                                            class="ri-check-double-line font-size-15"></i></span></p>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <div class="text-muted border-bottom pb-2 sm-desc">
                                    <h5 class="font-size-14 mb-1 text-truncate "><span class="read"><i
                                                class="ri-check-double-line font-size-15"></i></span> Read </h5>
                                    <p class="mb-1 font-size-13 text-muted">{{ getPreviousDay(msgitem.MODIFIED_ON) }} at,
                                        {{ getTime(msgitem.MODIFIED_ON) }}</p>
                                </div>
                                <div class="text-muted border-bottom pb-2 sm-desc pt-3">
                                    <h5 class="font-size-14 mb-1 text-truncate "><span class="read"><i
                                                class="ri-check-double-line font-size-15"></i></span> Delivered </h5>
                                    <p class="mb-1 font-size-13 text-muted">{{ getPreviousDay(msgitem.CREATED_AT) }} at,
                                        {{ getTime(msgitem.CREATED_AT) }}</p>
                                </div>

                            </template>

                            </div>
                        </div>
                    </div>
                </div>
                <div class="simplebar-mask" v-if="EditMessagInfoListData[0].IS_GROUP">
            <div class="simplebar-offset" style="right: -20px; bottom: 0px;">
                <div class="simplebar-content-wrapper"
                    style="height: 100%; padding-right: 20px; padding-bottom: 0px; overflow: hidden scroll;">
                    <div class="simplebar-content" v-for="msgitem,inedx in messageListByGroup" :key="inedx" style="padding: 0px 16px 16px;">
                        <div class="text-muted border-bottom pb-4 mb-3 sm-desc">
                           
                            <div class="ctext-wrap">
                                <div class="ctext-wrap-content">
                                    <div class="d-flex justify-content-between">
                                        <p class="mb-0">
                                            {{ msgitem.MESSAGE }}
                                        </p>
                                        <p class="chat-time mb-0 d-flex align-items-center"> <span
                                                class="align-middle me-1">{{ msgitem.CREATED_AT }}</span> <span class="read "><i
                                                    class="ri-check-double-line font-size-15"></i></span></p>
                                    </div>
                                </div>
                            </div>
                        </div>

                         <div class="d-flex align-items-center mb-3 w-100 justify-content-between" >
                            <h4 class="font-size-15">Read</h4>
                            <span class="read d-block text-center d-flex align-items-center">
                                <i class="ri-check-double-line font-size-15"></i>
                            </span>
                        </div>
                        <div class="d-flex align-items-center mb-3"   v-for="item in msgitem.IS_READ">
                            <div class="chat-user-img  align-self-center me-3 ms-0" :class="{ 'online': checkIsOnline(item.TO_USER_ID) == 1 , 'offline': checkIsOnline(item.TO_USER_ID) == 0 }">
                                <img :src="item.AVATAR == '' ? defaultImg : item.AVATAR" class="rounded-circle avatar-xs" alt="">
                                <span class="user-status"></span>
                            </div>
                            <div class="flex-grow-1 overflow-hidden">
                                <h5 class="text-truncate font-size-14 mb-1">{{ item.GROUP_NAME }}</h5>
                            </div>
                            <div class="font-size-11">{{ getPreviousDay(item.MODIFIED_ON) }} at,
                                        {{ getTime(item.MODIFIED_ON) }}</div>
                        </div>
                        <div class="d-flex align-items-center mb-2 w-100 justify-content-between border-bottom">
                        <h4 class="font-size-15">{{msgitem.delivered.length}} remaining</h4></div>
                        <div class="d-flex align-items-center mb-3 w-100 justify-content-between border-bottom pb-2">
                            <h4 class="font-size-15 mb-0">Delivered</h4>
                            <span class="read d-block text-center d-flex align-items-center">
                                <i class="ri-check-double-line font-size-15"></i>
                            </span>
                        </div>
                        <div class="d-flex align-items-center mb-3" v-for="deleveredItems in msgitem.delivered">
                            <div class="chat-user-img  align-self-center me-3 ms-0" :class="{ 'online': checkIsOnline(deleveredItems.TO_USER_ID) == 1 , 'offline': checkIsOnline(deleveredItems.TO_USER_ID) == 0 }">
                                <img :src="deleveredItems.AVATAR == '' ? defaultImg : deleveredItems.AVATAR" class="rounded-circle avatar-xs" alt="">
                                <span class="user-status"></span>
                            </div>
                            <div class="flex-grow-1 overflow-hidden">
                                <h5 class="text-truncate font-size-14 mb-1">{{ deleveredItems.GROUP_NAME }}</h5>
                            </div>
                            <div class="font-size-11">{{ getPreviousDay(deleveredItems.MODIFIED_ON) }} at,
                                        {{ getTime(deleveredItems.MODIFIED_ON) }}</div>
                        </div>
                    </div>
                </div>
            </div>
        </div>



                <div class="simplebar-placeholder" style="width: auto; height: 227px;"></div>
            </div>
            <div class="simplebar-track simplebar-horizontal" style="visibility: hidden;">
                <div class="simplebar-scrollbar" style="transform: translate3d(0px, 0px, 0px); display: none;"></div>
            </div>
            <div class="simplebar-track simplebar-vertical" style="visibility: hidden;">
                <div class="simplebar-scrollbar" style="transform: translate3d(0px, 0px, 0px); display: none;"></div>
            </div>
        </div>
        <!-- end User profile detail sidebar -->
       



    </div>
</div>
</template>
<script>
import { getPreviousDay } from "../assets/common/vue-common"
import { communicationStore } from "@/stores/Communication.js"
export default {
    props: {
        EditMessagInfoListData: {
            required: true
        }
    },
    data() {
        return {
            defaultImg: 'https://talygenv.github.io/Vue-UI-Content/images/DefaultImages/uc_user_new.jpg',
            messageListByGroup:[],
            onlineUsers: [],
        }
    },
    mounted(){

        this.messageListByGroup= this.transformChatLogs(this.EditMessagInfoListData);     
    },
    sockets:{
        connectedSockets: function (activeUsers) {
            this.onlineUsers = []
            this.onlineUsers = activeUsers
           
        },
    },
    methods:
    {
        checkIsOnline: function (itm) {
            var users = this.onlineUsers.findIndex(x => x.ID == itm.toString());
            if (users != undefined && users != -1) {
                return 1
            } else {
                return 0
            }
        },
        // Function to transform the data
        transformChatLogs(logs) {
        const groupedMessages = {};

        logs.forEach(log => {
            if(log.TO_USER_ID != this.store.getUserInformation.ID && log.IS_GROUP !=0)
            {
                const { MESSAGE, GROUP_NAME, IS_READ } = log;

                if (!groupedMessages[MESSAGE]) {
                    groupedMessages[MESSAGE] = {
                        MESSAGE,
                        IS_READ: [],
                        delivered: []
                    };
                }

                if (IS_READ) {
                    groupedMessages[MESSAGE].IS_READ.push(log);
                    } 
                    else {
                    groupedMessages[MESSAGE].delivered.push(log);
                }
            }
            
        });

        return Object.values(groupedMessages);
        },


        getTime(date) {
            date = new Date(date)
            const newInputDate = date;

            const timeStamp = new Date(newInputDate);
            return timeStamp.toLocaleTimeString(navigator.language, {
                hour: '2-digit',
                minute: '2-digit'
            })
        },
    },
    setup() {
        const store = communicationStore()
        return {
            getPreviousDay,
            store,
        }
    }
}
</script>