                        <!-- End profile user -->
                        <!-- Start user-profile-desc -->
                        
                        <div class="p-3 user-profile-desc pt-0" data-simplebar="init" >
                            <div class="simplebar-wrapper" style="margin: 0px -16px -16px;">
                                <div class="simplebar-height-auto-observer-wrapper">
                                    <div class="simplebar-height-auto-observer">
                                    </div>
                                </div>
                                <div class="simplebar-mask">
                            <div class="simplebar-offset" style="right: 0px; bottom: 0px;">
                        <div class="simplebar-content-wrapper" style="height: 100%; padding-right: 0px; padding-bottom: 0px; overflow: hidden;">
                            <div class="simplebar-content" style="padding: 0px 16px 16px;">
                                <span style="color: red;" v-if="searchedMessageList.length==0">No record found</span>
                                <div class="text-muted border-bottom pb-2 sm-desc" v-for="searchItems, searchIndex in searchedMessageList" :key="searchIndex">
                                    <h5 class="font-size-13 mb-1 text-truncate d-flex justify-content-between">
                                        <span>{{ getPreviousDay(searchItems.CREATED_AT) }} at, {{ getTime(searchItems.CREATED_AT) }}</span>
                                    </h5>
                                    <p class="mb-1 font-size-14">
                                        <span class="read"><i class="ri-check-double-line font-size-15"></i></span>
                                        <span v-html="highlightMessage(searchItems.MESSAGE, searchMessageTxt)" v-on:click="searchLocalMessages(searchItems)"></span>
