</div>

</template>

<script>
import DataService from '../services/DataService';
import VueEasyLightbox from 'vue-easy-lightbox'
const axios = require('axios').default;
import LightBox from 'vue-image-lightbox' 
import { getPreviousDay } from "../assets/common/vue-common"
export default {
    name: "ContactDetail",
    props: {
        chatRoom: {
            type: Object
        },
        store: {
            type: Object
        },
        isGroupMessageSearch:{
            type:Boolean
        }
    }, 
    components: {
        VueEasyLightbox,
        LightBox,
    },
    data() {
        return {
            defaultImg: 'https://talygenv.github.io/Vue-UI-Content/images/DefaultImages/uc_user_new.jpg',
            ChatFileList: [], 
            activeMediaType: 'Media',
            editGroupName:false,
            editGroupDescriptions:false,
            sender: this.store.GetSenderDetail,
            IsgroupMemeber:false,
            MemberList:[],
            AllContactList :[],
            selectedMembers : [],
            ImageItemToDownload:'',
            imageLightBoxData:'',
            imgs: [],  // Img Url , string or Array of string
            visible: false,
            index: 0,  // default: 0
            searchedMessageList:[],
            searchMessageTxt:'',
            groupimage: [],
            uploadUrl: process.env.VUE_APP_DOCSTORAGE_API + '/UploadStreamNew',
            AttachmentFileDetail:[],
            token: '',
            filesdetails:[],
            isShow:false,
            media:
                [
                    { // For image
                        thumb: '',
                        src: '',
                        caption: '', // Optional
                        // srcset: '...' // Optional for displaying responsive images
                    },
                    { // For video
                        thumb: '',
                        sources: [
                        {
                            src: '',
                            type: ''
                        }
                        ],
                        type: "",
                        caption: '',
                        width: 800, // required
    height: 600, // required
                        // autoplay: true //Optional to autoplay video when lightbox opens
                    }
                ]
            }
    },    
    sockets: {
        getMessageSearcgDataOut: function (result) {
            
            this.searchedMessageList = result;
        },
        allChatAttachments: function (fileList) {

            var vm = this;
            vm.ChatFileList = fileList ? fileList.resultSet : []
            console.log('ChatFileList', vm.ChatFileList)
        },
    },
    created: async function () {
        this.chatRoom.url  = this.chatRoom.IS_GROUP ?this.chatRoom.url :  this.chatRoom.AVATAR
        await this.store.userByGroupId(`emaiId=${this.store.getUserInformation.Email}&isGroup=1&room=${this.chatRoom.GROUP_ID}`)
        this.getUserlist(this.store.getUserByGroupId)
        this.ChatLinkClick();
        this.store.getContact(`search=&pageSize=&pageNum=&sortBy=&sortExp=&type=${'company'}&city=&departmentId=`)
    },
    mounted() {

    },
    methods: {
        searchLocalMessages: async function (itm) {
            var vm = this;
            vm.$parent.searchClick(itm);
        },
        //search 
        clearSearchedText: function()
        {
            this.searchMessageTxt='';
            this.searchedMessageList=[];
        },
        closeSearchPop: function(){
            this.isGroupMessageSearch=false;
        },
        highlightMessage(message, query) {
            if (!query) return message;
            const regex = new RegExp(`(${query})`, 'gi');
            return message.replace(regex, '<b>$1</b>');
            //this.$emit('messageHighlight',{'message':message,'query':query})
        },
        SearchGroupMessage: function()
        {
            var vm = this;
            var param = {};
            if (vm.searchMessageTxt.trim() != '') {
                param.companyId = vm.sender.companyId;
                param.id = vm.sender.id;
                param.searchKeyword = vm.searchMessageTxt;
                param.groupId=vm.chatRoom.GROUP_ID;
                vm.$socket.emit('search group message list', param, function () { });
            }
        },
        getTime(date) {
            date = new Date(date)
            const newInputDate = date;

            const timeStamp = new Date(newInputDate);
            return timeStamp.toLocaleTimeString(navigator.language, {
                hour: '2-digit',
                minute: '2-digit'
            })
        },
        //end search
        triggerFileInput() {
      this.$refs.fileInput.click();
    },
        isImageAndVideoFiles(extension)
        {
            return ['.jpg', '.png','.mp4','.3gp'].includes(extension);
        },
        isImage(extension) {
            return ['.jpg', '.png'].includes(extension);
        },
        downloadImage: function (iym) {
            debugger
            // var vm=this;

            var param = `fileId=${this.ImageItemToDownload.STORAGE_FILE_ID}&IsVersion=0`
            DataService.DownloadFileSingle(param)
                .then(response => {
                    var fileURL = window.URL.createObjectURL(new Blob([response.data]));
                    var fileLink = document.createElement('a');
                    fileLink.href = fileURL;
                    fileLink.setAttribute('download', (this.ImageItemToDownload.STORAGE_FILE_NAME));
                    document.body.appendChild(fileLink);
                    fileLink.click();
                })

        },
        handleHide() {
            debugger
            this.ImageItemToDownload=''
            // this.visible = false
            this.isShow = false;

        },
        openLightbox: function (path, ext, Imagename, itm, indx) {

                // Reset the media array and set the visible flag to true
                this.media = [];
                this.isShow = true;

                // Store the clicked item to download if necessary
                this.ImageItemToDownload = itm;

                // Add all media (images and videos) to the media array
                this.ChatFileList.forEach((fileItem, fileIndex) => {
                    if (fileItem.FILEEXTENSION === '.jpg' || fileItem.FILEEXTENSION === '.png') {
                        this.media.push({
                            thumb: fileItem.FILEPATH,
                            src: fileItem.FILEPATH,
                            caption: fileItem.STORAGE_FILE_NAME, // Optional
                            // srcset: '...' // Optional for displaying responsive images
                        });
                    } else if (this.isVideo(fileItem.FILEEXTENSION)) {
                        this.media.push({
                            thumb: fileItem.FILEPATH,
                            sources: [
                                {
                                    src: fileItem.FILEPATH,
                                    type: 'video/mp4'
                                }
                            ],
                            type: "video",
                            caption: fileItem.STORAGE_FILE_NAME,
                            width: 800, // required
                            height: 600, // required
                            autoplay: true // Optional to autoplay video when lightbox opens
                        });
                    }
                });

                // Find the correct index of the clicked media in the media array
                const clickedIndex = this.media.findIndex(media => media.caption === itm.STORAGE_FILE_NAME && media.src === itm.FILEPATH);

                // Reorder the media array to place the clicked item first
                if (clickedIndex !== -1) {
                    const clickedItem = this.media.splice(clickedIndex, 1)[0];
                    this.media.unshift(clickedItem);
                    this.index = 0;
                } else {
                    this.index = -1;
                }

                // Set the Lightbox visibility and the imageLightBoxData
                this.imageLightBoxData = itm;
            },

            isVideo: function (ext) {
                return ext === '.mp4'; // Add other video extensions if necessary
            },

        ChatLinkClick() {

            var vm = this;

            var paramsObj = {};
            paramsObj.room = vm.chatRoom.GROUP_ID;
            paramsObj.companyId = vm.sender.companyId;
            paramsObj.id = vm.sender.id;
            paramsObj.emaiId = vm.sender.emaiId;
            vm.$socket.emit('chat All Attachments', paramsObj);

        },
        removeCurrentUserlist(data){
            return data.filter(user=>user.USER_ID != this.sender.id)

        },
        mediaType(event) {
            this.activeMediaType = event
        },
        closepopup(){
            this.selectedMembers=[]; 
        this.IsgroupMemeber=false; 
        }, 
        previewImage: function(event) {
        debugger
         var input = event.target;
         if (input.files) {
         var reader = new FileReader();
          reader.onload = (e) => {
          this.chatRoom.url = e.target.result;
        }
        this.AttachmentFileDetail.push(input.files[0]);
        this.uploadimage();
        reader.readAsDataURL(input.files[0]);
        }
    },
        uploadimage: async function () {
            debugger
           // this.token = this.store.getUserInformation.Token
            var vm = this; 
            var formData = new FormData();
            formData.append("fileId", 0);
            formData.append("folderId", 0);
            formData.append("moduleName", 'Communication');
            formData.append("subModuleName", '');
            formData.append("isGlobal", 0);
            formData.append("file", vm.AttachmentFileDetail[0]);
            // vm.closeAttachmentPop();
            axios.post(this.uploadUrl, formData, {
                headers: {
                    'Authorization': 'Bearer ' + this.store.getUserInformation.Token
                },
            })
                .then((response) => {  
                    if (response.data.errorMsg == null) {
                        debugger
                        if (JSON.parse(response.data.FileID)[0].Value > 0) {
                            vm.SavedFileId = JSON.parse(response.data.FileID)[0].Value;
                            console.log()
                            this.filesdetails.push({
                             fileType: response.data.FileType === 'image' ? 'attachment' : response.data.FileType,
                             file: response.data.FileName,
                            size: response.data.FileSize,
                            url: response.data.FilePath,
                            from: vm.sender.id,
                            typePath: response.data.FileType,
                            fileId: vm.SavedFileId
                            }); 
                        }
                        this.UpdateGroupDetail();
                        vm.AttachmentFileDetail = []
                    } else {
