        /**
         * user side video call start
         * @param {*} contect
         */
        videoCreate(contect) {
            //this.videoCallShow = true
            this.callToUserInfo = contect
            var receiver = contect.USER_ID;
            var params = {
                emaild: this.store.getUserInformation.Email,
                name: this.store.getUserInformation.ame, // Corrected typo in property name
                id: this.store.getUserInformation.ID,
                type: "web",
                companyId: this.store.getUserInformation.CompanyId,
                imgPath: this.store.getUserInformation.Avatar,
                userType: 'vdrt',
                alias: this.store.getUserInformation.ID + "_" + receiver,
                room: this.store.getUserInformation.ID + "_" + receiver,
                isSender: 1,
                IdList: this.store.getUserInformation.ID + "_" + receiver,
                IS_GROUP: 0,
                calltype:contect.calltype
            };
            this.callType = contect.calltype
            if (contect.IS_GROUP) {
                params.alias = contect.GROUP_NAME_ALIAS
                params.IS_GROUP = 1
                params.room = contect.room
                this.videoPopUpCall(contect, 1, params.alias)
            } else {
                params.alias = this.store.getUserInformation.ID + "_" + receiver
                this.$socket.emit('createRoom', params);
            }

        },
        attachMediaStream(element, stream) {
            console.log('DEPRECATED, attachMediaStream will soon be removed.');
            element.srcObject = stream;
        },
        setup_local_media(param, callback, errorback) {
            this.started = setInterval(this.clockRunning, 1000);
            if (this.localMediaStream !== null) {
                // If we've already been initialized
                if (callback) callback();
                return;
            }

            var isSafari = /constructor/i.test(window.HTMLElement) ||
                (function (p) {
                    return p.toString() === "[object SafariRemoteNotification]";
                })(
                    !window['safari'] || (typeof isSafari !== 'undefined' && window['safari'].pushNotification)
                );

            if (!isSafari)
             {
                navigator.getUserMedia = (navigator.getUserMedia ||
                    navigator.webkitGetUserMedia ||
                    navigator.mozGetUserMedia ||
                    navigator.msGetUserMedia);

                    navigator.getUserMedia({
                         audio : {
                            sampleSize: 8,
                            echoCancellation: {
                                exact: true
                            },
                            noiseSuppression: false,
                            autoGainControl: true
                        },
                        video: param.calltype != "audio" ? true : false
                    },
                        (stream) => {
                            if(param.calltype == "audio"){
                                this.audioCallPopupShow = true
                            }else{
                                this.videoCallShow = true
                            }
                            setTimeout(() => {
                                if (param.isSender == 1) {
                                    this.$socket.emit('video call', param);
                                }
                                this.localMediaStream = stream;
                                if(param.calltype == "audio"){
                                this.$refs.localAudio.srcObject = stream
                                this.attachMediaStream(this.$refs.localAudio, stream);
                                }else{
                                this.$refs.localVideo.srcObject = stream
                                this.attachMediaStream(this.$refs.localVideo, stream);
                                }

                                if (param.isSender != 1) {
                                    this.$socket.emit('joinVideo', {
                                        "channel": param.token,
                                        "userdata": this.store.getUserInformation.ID,
                                        "calltype":param.calltype
                                    });
                                }
                                if (callback) callback();
                            }, 100)

                        },
                        () => {
                            // User denied access to audio/video
                            console.log("Access denied for audio");
                            this.ShowAlert(this.$t("Camera_Access_Connect"), "failure", true, this.$t("Alert"));
                            //$(".disconnectVideo").trigger("click");
                            if (errorback) errorback();
                        }
                    );




            }
            else
             {
                try {
                    navigator.getUserMedia = (navigator.getUserMedia ||
                        navigator.webkitGetUserMedia ||
                        navigator.mozGetUserMedia ||
                        navigator.msGetUserMedia);

                    navigator.mediaDevices.getUserMedia({
                        "audio": {
                            sampleSize: 8,
                            echoCancellation: {
                                exact: true
                            },
                            noiseSuppression: false,
                            autoGainControl: true
                        },
                        "video": {
                            mirrored: false,
                            frameRate: {
                                ideal: 10,
                                max: 15
                            }
                        }
                    },
                        (stream) => {
                            this.$refs.localVideo.srcObject = stream
                            // User accepted access to audio/video
                            console.log("Access granted to audio/video");
                            if (param.isSender == 1) {
                                this.$socket.emit('video call', param);
                            }

                            this.localMediaStream = stream;

                            this.attachMediaStream(this.$refs.localVideo, stream);

                            if (param.isSender != 1) {

                                this.$socket.emit('joinVideo', {
                                    "channel": param.token,
                                    "userdata": this.store.getUserInformation.ID
                                });
                            }
                            if (callback) callback();
                        },
                        () => {
                            // User denied access to audio/video
                            console.log("Access denied for audio/video");
                            this.ShowAlert(this.$t("Camera_Access_Connect"), "failure", true, this.$t("Alert"));
                            //$(".disconnectVideo").trigger("click");
                            if (errorback) errorback();
                        }
                    );

                } catch (e) {
                    alert(e);
                }
            }

        },

        setupIceCandidateHandler(peer_connection, peer_id) {
            peer_connection.onicecandidate = (event) => {

                if (event.candidate) {

                    this.$socket.emit('relayICECandidate', {
                        peer_id: peer_id,
                        ice_candidate: {
                            sdpMLineIndex: event.candidate.sdpMLineIndex,
                            candidate: event.candidate.candidate,
                        },
                    });
                }
            };
        },
        setupAddStreamHandler(peer_connection) {
            peer_connection.onaddstream = (event) => {
                console.log("onAddStream", event);
                this.showPlaceholder = false;
                this.attachMediaStream(this.callType=='audio' ? this.$refs.remoteAudio : this.$refs.remoteVideo, event.stream)

                //this.peer_media_elements[peer_id] = remote_media;
            };
        },
        createAndSendOffer(peer_connection, peer_id) {
            peer_connection.createOffer()
                .then((local_description) => {
                    return peer_connection.setLocalDescription(local_description);
                })
                .then(() => {
                    this.$socket.emit('relaySessionDescription', {
                        peer_id: peer_id,
                        session_description: peer_connection.localDescription,
                    });
                })
                .catch((error) => {
                    console.log("Error sending offer: ", error);
                });
        },
        VideoCallPickupMethod(type) {


            var room_accept = this.incomingVideoCallinfo.roomId + '~' + this.incomingVideoCallinfo.actRoom + '~' + this.incomingVideoCallinfo.actRoomId
            var receiverId_accept = this.incomingVideoCallinfo.fromUserId;
            var contactname_accept = this.incomingVideoCallinfo.fromName;
            var newparam = new Object();
            newparam.receiver = receiverId_accept;
            newparam.contactname = contactname_accept;
            newparam.id = this.store.getUserInformation.ID;
            newparam.calltype = this.incomingVideoCallinfo.calltype;
            if (type == "tokBox") {
                newparam.path = room_accept;
                this.$socket.emit('add video on chat tokBox', newparam);
            } else {
                newparam.room = room_accept;
                this.$socket.emit('add video on chat', newparam);
            }
            // Emit an event for the accepted call ocket.emit('already pick video call', newparam);
            // Remove the notification $('#' + receiverId_accept + ".vca_mainwrapper").remove();},
        },

        clockRunning() {
            this.videoCallCounting = this.videoCallCounting + 1
        },
        GetStatus(roomId) {
            const chatUserElement = roomId;
            if (chatUserElement) {
                this.$socket.emit('get Open Chat User Status', chatUserElement);
            }
        },
        disconnectVideo() {
            clearInterval(this.started)
            this.videoCallCounting = 0
            if (this.localMediaStream != null) {
                this.localMediaStream.getTracks().forEach(function (track) {
                    track.stop();
                });
                this.localMediaStream = null;
                this.peers = {};
                this.peers = {};
            }
            this.videoCallShow = false
            this.audioCallPopupShow = false
            this.inComingCallingPopup = false
            this.incomingaudiocallpopup= false
            this.$socket.emit('video call reject', this.incomingOutGoingParams)
        },
        handleRejectNewVideo() {
            if (this.localMediaStream != null) {
