</template>

<script>
// import {
//     ValidationProvider,
//     //extend,
//     ValidationObserver
// } from 'vee-validate';

import TwilioSetup from './TwilioSetup.vue';
import ChatSetupSetup from './ChatSetup.vue';

export default {
    name: "Setup",
    components: {
        //ValidationProvider,
        //ValidationObserver,
        TwilioSetup,
        ChatSetupSetup
    },
    props: {
        store: {
            type: Object
        },
        selectedFilter:{
            type:String
        }
    },
    data() {
        return {
            deviceSetuppopupShow:false,
            deviceType:'',
            openChat:false,
            pageTitle:''
        }
    },
    created() {

    },
    watch: { 
        selectedFilter: function(newVal, oldVal) { // watch it
            console.warn(oldVal)
            this.deviceType = ''
            this.deviceSetuppopupShow = false
            this.pageTitle = newVal
        }
        },
    //   computed: {
    //     deviceSetupTitle: function () {
    //             console.log("props")
    //             
    //         return this.selectedFilter
    //     }
    // },
    methods: {
        deviceSelect(e) {
            var vm = this;
            vm.deviceType = e
            vm.deviceSetuppopupShow =false
        },
        deviceSetupPageShow(){
            if(this.pageTitle=='Chat'){
                this.openChat = true
                this.deviceSetuppopupShow = false
            }else{
                this.deviceSetuppopupShow = true
            }
        }
    },

}
</script>
