import { defineStore } from "pinia";
import DataService from "../services/DataService";
import { useloggedUserInfo } from "./UserInfo"
import { Device } from '@twilio/voice-sdk';

export const communicationStore = defineStore({
    id: "communication",
    state: () => ({
        leftSideFilters: [],
        contactList: [],
        hearderContactNameFilter:[],
        loading:false,
        contactDetails:'',
        responseSubmitContactInfo:'',
        callTabsInformationState:[],
        crmDetailRecordState:[],
        timerStart:0,
        timerInterval:'',
        addCommentForCallRes:'',
        countryState:[],
        recentCallState:[],
        callToken:'',
        deviceSetup:'',
        incomingAndOutGoingCall:false,
        incomingCallData:'',
        incomingCall:false,
        outGoingcallData:'',
        callEnded:false,
        moduleDropdownState:[],
        subModuleDropdownState:[],
        incomingcallNumber:'',
        addUpdateGroupResState:'',
        groupListState:[],
        userByGroupIdState:[],
        deleteGroupResState:'',
        twilioListaState:[],
        globalSearch:'',
        twilioAccountDetails:'',
        blobUserUrl:'',
        userInfo:'',
        RecentChatuser:[]
        
    }),
    getters: {
        getRecentChatUser:(state)=>{
            
            return state.RecentChatuser
        },
        getLeftSideFilter: (state) => {
            return state.leftSideFilters
        },
        getContactList: (state) => {
            return state.contactList
        },
        getHearderContactNameFilter:(state)=>{
            return state.hearderContactNameFilter
        },
        getUserInformation: (state) => {
            const { user } = useloggedUserInfo();
            if (user) {
                let profile = user.profile;
                return {
                    ID: profile.id,
                    Token: user.access_token,
                    CompanyId: profile.companyId,
                    ame: profile['http://schemas.xmlsoap.org/ws/2005/05/identity/claims/name'],
                    Name: profile['http://schemas.xmlsoap.org/ws/2005/05/identity/claims/name'],
                    UserType: profile['http://schemas.microsoft.com/ws/2008/06/identity/claims/role'],
                    TimeFormat: profile.timeFormat,
                    Email: profile['http://schemas.xmlsoap.org/ws/2005/05/identity/claims/emailaddress'],
                    Avatar: profile.avatar,
                    ContainerName: profile.containerName,
                    CompanyLogo: profile.companylogo,
                    Currency: profile.Currency,
                    Gender: profile.gender,
                    PageSize: profile.PageSize,
                    IsWithCC: profile.IsWithCC,
                    DoNotShowDirtyCheck: profile.DoNotShowDirtyCheck,
                    IsClientCA: profile.IsClientCA,
                    CompanyStatusCode: profile.companyStatusCode,
                    LoginStatus: profile.loginStatus,
                    TimeZoneStatus: profile.TimeZoneStatus,
                    IsInTrial: profile.IsInTrial,
                    IsDefault: profile.isdefault,
                    TimeZone: profile.timezone,
                    MenuType: profile.menutype,
                    MenuTypeCode: profile.menutype,
                    Theme: profile.theme,
                    OrgComapanyId: profile.OrgComapanyId,
                    CompanyName: profile.companyName,
                    EnableMyDashboard: profile.EnableMyDashboard,
                    FlagNotificationCheck: profile.FlagNotificationCheck,
                    IsVendor: profile.IsVendor,
                    IsInitialSetup: profile.initialSetup,
                    DateFormat: profile.dateFormat,
                    IsContextMenuClosed: true
                }
            }
            return null;
        },
        GetSenderDetail:()=>{
            const { user } = useloggedUserInfo();
            if (user) {
                let profile = user.profile;
                let sender = {  
            companyId:profile.companyId,
            id :profile.id,
            emaiId:profile['http://schemas.xmlsoap.org/ws/2005/05/identity/claims/emailaddress'],
            name : profile['http://schemas.xmlsoap.org/ws/2005/05/identity/claims/name'],
            imgPath :profile.Avatar,
            type:'web'
                }
            return sender;
        }
        return null;
        },
        getLoadingStatus:(state)=>{
            return state.loading
        },
        getContactDetails:(state) =>{
            return state.contactDetails
        },
        getResponseSubmitContactInfo:(state)=>{
            return state.responseSubmitContactInfo
        },
        getCallTabsInformation:(state)=>{
            return state.callTabsInformationState
        },
        getCrmDetailRecord:(state)=>{
            return state.crmDetailRecordState
        },
        getAddCommentForCallRes:(state)=>{
            return state.addCommentForCallRes
        },
        getcountry:(state)=>{
            return state.countryState
        },
        getRecentCall:(state)=>{
            let recentCallArray = []
            if(state.recentCallState.length > 0){
                const groups = state.recentCallState.reduce((groups, data) => {
                    const date = data.CREATED_AT.split('T')[0];
                    if (!groups[date]) {
                      groups[date] = [];
                    }
                    groups[date].push(data);
                    return groups;
                  }, {});
                  recentCallArray = Object.keys(groups).map((date) => {
                    return {
                      date,
                      calls: groups[date]
                    };
                  });
            }
            return recentCallArray 
        },
        getIncomingAndOutGoingCall:(state)=>{
            return state.incomingAndOutGoingCall
        },
        getIncomingCallData:(state)=>{
            return state.incomingCallData
        },
        getIncomingCallStatus:(state)=>{
            return state.incomingCall
        },
        getOutGoingcallData:(state)=>{
            return state.outGoingcallData
        },
        getCallEnded:(state)=>{
            return state.callEnded
        },
        getModuleDropdown:(state)=>{
            return state.moduleDropdownState
        },
        getSubModuleDropdown:(state)=>{
            return state.subModuleDropdownState
        },
        getAddUpdateGroupRes:(state)=>{
            return state.addUpdateGroupResState
        },
        getGroupList:(state)=>{
            return state.groupListState
        },
        getUserByGroupId:(state)=>{
            return state.userByGroupIdState
        },
        getDeleteGroupRes:(state)=>{
            return state.deleteGroupResState
        },
        getTwilioList:(state)=>{
            return state.twilioListaState
        },
        getGlobalSearch:(state)=>{
            return state.globalSearch
        },
        getTwilioAccountDetails:(state)=>{
            return state.twilioAccountDetails
        },
        getBlobUserUrl:(state)=>{
            return state.blobUserUrl
        }
    },
    actions: {
        //****Call setup incoming And outGoing call From store
        async intitializeDevice() {
            this.deviceSetup = new Device(this.callToken, {
                logLevel: 1,
                codecPreferences: ["opus", "pcmu"],
            })
            this.addDeviceListeners(this.deviceSetup);
            this.deviceSetup.register();
        },
        async addDeviceListeners(deviceData) {
            await deviceData.on("registered", function () {
                console.log("this.Device Ready to make and receive calls!");
            });

            await deviceData.on("error", function (error) {
                console.log("this.Device Error:", error);
            });
            deviceData.on("incoming", this.handleIncomingCall);

            //await deviceData.audio.on("deviceChange", this.updateAllAudioDevices(this.deviceSetup));

            // Show audio selection UI if it is supported by the browser.
            if (this.deviceSetup.audio.isOutputSelectionSupported) {
                //audioSelectionDiv.classList.remove("hide");
            }
        },
        async makeOutgoingCall(params) {
            if (this.deviceSetup) {
                this.outGoingcallData = await this.deviceSetup.connect({
                    params
                });
                this.incomingAndOutGoingCall = true
    
            } else {
                //log("Unable to make call.");
            }
        },
        handleIncomingCall(call) {
            this.incomingAndOutGoingCall = true
            //console.log("incoming");
            this.incomingCall = true
            this.incomingCallData = call
            this.incomingcallNumber = this.incomingCallData.parameters.From

        },
        hangup(){
            this.incomingAndOutGoingCall = false
            if (this.outGoingcallData || this.incomingCallData) {
                this.incomingCallData ? this.incomingCallData.disconnect() : this.outGoingcallData.disconnect()
            } else {
                this.deviceSetup ? this.deviceSetup.disconnect() : '';
            }
            this.callEnded = true
            this.clearInterval()
            this.outGoingcallData = ''
            this.incomingCallData = ''
        },
        //****end call 


        async leftSideFilter() {
            this.loading = true
            const { data } = await DataService.GetDepartmentsAndLocations('type=user')
            data ?  data.Locations.map((item)=>{
                item.department_name = item.user_city
                return item
            }) :''
            if(data){
            this.leftSideFilters = [{
                type: "Department",
                data: data.Departments
            },
            {
                type: "Location",
                data: data.Locations
            }]
        }
            this.loading = false
        },
        //****conctact Page ....
        async getContact(params) {
            this.loading = true
            this.hearderContactNameFilter = []
            const { data} = await DataService.CompanyContact(params)
            this.contactList = JSON.parse(data)
            let index = this.contactList.findIndex(item => item.USER_ID == this.getUserInformation.ID);
                    
                    if (index != -1) {
                        this.contactList.splice(index, 1);
                        }
            // this.contactList = this.contactList.filter((item) => item.EMAIL_ID !== null)
            // this.contactList.map((item)=>{
            //     if(item.CONTACTNAME){
            //     let obj = item.CONTACTNAME ? this.hearderContactNameFilter.find(data=>data==item.CONTACTNAME.substring(0, 1).toUpperCase()) : null
            //     if(!obj){
            //         this.hearderContactNameFilter.push(item.CONTACTNAME.substring(0, 1).toUpperCase())
            //     }
            // }
            
            // })
            this.loading = false
        },
        async groupList(params){
            this.loading = true
            const { data } = await DataService.GetAllGroups(params)
            console.log("data",data)
            this.groupListState = data
            this.loading = false
        },
        async userByGroupId(params){
            console.log("groupList",params)
            this.loading = true
            const { data } = await DataService.GetUserByGroupId(params)
            console.log("data",data)
            this.userByGroupIdState = data
            this.loading = false
        },
        async addOrUpdateGroup(params){
            this.loading = true
            const { data } = await DataService.updateRoom(params)
            this.addUpdateGroupResState = data
            this.loading = false
        },
        async deleteGroup(params){
            this.loading = true
            const { data } = await DataService.DeleteGroup(params)
            this.deleteGroupResState = data
            this.loading = false
        },
        //****contact Page end
        async NexoCall(param) {
            this.loading = true
            const { data} = await DataService.NexoCall(param)
            console.log("data",data)
            this.loading = false
            
        },
        async VonageCall(param) {
            this.loading = true
            const { data} = await DataService.VonageCall(param)
            console.log("data",data)
            this.loading = false
            
        },
        async contactInformation(params){
            this.loading = true
            const { data } = await DataService.GetUserDetailByPhone(params)
            let jsonData = data ?  data[0].Value : ''
            this.contactDetails = JSON.parse(jsonData).JsonData[0]
            this.loading = false
        },
        async addContactInformation(params){
            this.loading = true
            const { data } = await DataService.SaveUnifiedContact(params)
            this.responseSubmitContactInfo = data[0]
            this.loading = false
        },
        async addCommentForCall(params){
            this.loading = true
            const { data } = await DataService.SaveCallMapping(params)
            console.log("testttt",data)
            this.addCommentForCallRes = data[0]
            this.loading = false
        },
        async countryGet(){
            this.loading = true
            var url = `id=&moduleName=&type=country&search=&refId=`;
            const { data } = await DataService.GetDDLData(url)
            this.countryState = data ? data.DATA :''
            this.loading = false
        },
        async callTabsInformation(event){
            if(event.tab!='crm'){
            this.loading = true
            let   url = ''
            if(event.tab=='Ticket'){
                url = 'GetUnifiedTicketingListing'
            }
            else if(event.tab=='project'){
                url = 'GetUnifiedProjectListing'
            }
            const { data } = await DataService.GetUnifiedTicketingListing(event.params,url)
            let jsonData = data ?  data[0].Value : ''
            this.callTabsInformationState = jsonData ? JSON.parse(jsonData).DATA : []
            this.loading = false
        }
        },
        async crmDetailRecord(params){
            this.loading = true
            let url = ''
            let type = ''
            if(params.tab=='Leads'){
            url ='GetUnifiedLeadDetail'
            }
            else if(params.tab=='CRM_DEALS'){
                url ='GetUnifiedDealDetail'
            }
            else if(params.tab=='Quote'){
                url ='GetUnifiedOrderListing'
                type = 'QO'
            }
            else if(params.tab=='PurchaseOrderWithoutRef'){
                url ='GetUnifiedOrderListing'
                type = 'PO'
            }
            else if(params.tab=='CRM_SALES_ORDERS'){
                url ='GetUnifiedOrderListing'
                type = 'SO'
            }
            else if(params.tab=='Invoices'){
                url ='GetUnifiedOrderListing'
                type = 'INV'
            }

            const { data } = await DataService.GetUnifiedOrderListing(params.params+'&type_code='+type,url)
            let jsonData = data ?  data[0].Value : ''
            this.crmDetailRecordState = jsonData ? JSON.parse(jsonData).DATA : []
            this.loading = false
        },
        async callTimerStart() {
            clearInterval(this.timerInterval)
            this.timerInterval = setInterval(() => {
                this.timerStart++
            }, 1000)
        },
        async recentCalls(params){
            this.loading = true
            const { data } = await DataService.getRecentcalls(params)
            this.recentCallState= data ?  data[0].Value ? JSON.parse(data[0].Value) : [] : []

            this.loading = false
        },
        clearInterval() {
            clearInterval(this.timerInterval)
        },
        async moduleDrowdown(params){
            this.loading = true
            const { data } = await DataService.DropDownForAddComment(params)
            this.moduleDropdownState =data.data
            this.loading = false
        },
        async subModuleDrowdown(params){
            this.loading = true
            const { data } = await DataService.DropDownForSubmodule(params)
            let jsonData = data ? data[0].Value : ''
            this.subModuleDropdownState =jsonData ? JSON.parse(jsonData).DATA : []
            this.loading = false
        },
        async twilioList(params){
            this.loading = true
            const { data } = await DataService.TwilioList(params)
            
            this.twilioListaState = data ? JSON.parse(data[0].value) : []
            console.log("this.twilioListaState",this.twilioListaState)
            this.loading = false
        },
        async GetTwilioUserConfigDetail(params){
            const { data } = await DataService.GetTwilioUserConfigDetail(params)
            let jsonData = data ? JSON.parse(data[0].value) : ''
            this.twilioAccountDetails = jsonData ? jsonData.DATA[0] : ''
        },
        async GetBlobUrl (params) {
            const {data}=await  DataService.GetBlobUrl();
            console.log('this.userInfo',this.getUserInformation.ContainerName)
            this.blobUserUrl = data[0].CONFIG_DATA_VALUE + this.getUserInformation.ContainerName + '/' + 'Upload' + '/' + 'UserProfileImage' + '/'
        },
        
        // Usage example:
        
        
    },
});
