<template>

    <!-- Start User chat -->
    <div class="user-chat w-100 overflow-hidden ">


        <ForwardPopup v-if="isForwardToUser" ></ForwardPopup>
        <!-- start chat conversation section -->
        <div class="w-100 overflow-hidden position-relative">
            <div class="px-4 pt-3 pb-3 border-bottom user-chat-topbar">
                <div class="row align-items-center">
                    <div class="col-sm-4 col-8">
                        <div class="d-flex align-items-center" v-if="isDataNotNull">
                            <div class="d-block d-lg-none me-2 ms-0" @click="removeClass()">
                                <a href="javascript: void(0);" class="user-chat-remove text-muted font-size-16 p-2"><i
                                        class="ri-arrow-left-s-line"></i></a>
                            </div>
                            <div class="me-3 ms-0 user-profile-show"> 
                                    <img v-if="chatHistoryDetails.groupDetail.IS_GROUP" :src="chatHistoryDetails.groupDetail.url == '' ? defaultImg : chatHistoryDetails.groupDetail.url"
                                    class="rounded-circle avatar-xs" alt="">
                                    <img v-else :src="chatHistoryDetails.groupDetail.AVATAR == '' ? defaultImg : chatHistoryDetails.groupDetail.AVATAR"
                                    class="rounded-circle avatar-xs" alt="">
                            </div>
                            <div class="flex-grow-1 overflow-hidden">
                                <h5 class="font-size-16 mb-0 text-truncate" @click="$parent.detailpopup({data:chatHistoryDetails.groupDetail,show:true})"><a
                                        class="text-reset user-profile-show">{{ chatHistoryDetails.groupName }}</a> </h5>
                                <p class="mb-0" v-if="chatHistoryDetails.groupDetail.IS_GROUP"><i
                                        class="fas fa-user me-1"></i> {{ getMembers(chatHistoryDetails.groupDetail) + ' Members' }}</p>
                            </div>
                        </div>
                    </div>
                    <div class="col-sm-8 col-4">
                        <ul class="list-inline user-chat-nav text-end mb-0">


                            <li class="list-inline-item d-none d-lg-inline-block me-2 ms-0" @click="videoCreate('audio')">
                                <button type="button" class="btn nav-btn greenbg" data-bs-toggle="modal"
                                    data-bs-target="#audiocallModal">
                                    <i class="fas fa-phone-alt"></i>
                                </button>
                            </li>

                            <li class="list-inline-item d-none d-lg-inline-block me-2 ms-0" @click="videoCreate('video')">
                                <button type="button" class="btn nav-btn bluebg" data-bs-toggle="modal"
                                    data-bs-target="#videocallModal">
                                    <i class="fas fa-video"></i>
                                </button>
                            </li>



                            <li class="list-inline-item">
                                <div class="dropdown">
                                    <button class="btn nav-btn dropdown-toggle darkbg" type="button"
                                        data-bs-toggle="dropdown" aria-haspopup="true" aria-expanded="false"
                                        ref="globalCahtPopup" @click="globalAction = !globalAction">
                                        <i class="fas fa-ellipsis-v"></i>
                                    </button>
                                    <div class="dropdown-menu dropdown-menu-end d-block" style="float: left;"
                                        v-if="globalAction"
                                        v-closable="{ exclude: ['globalCahtPopup'], handler: 'globalPopupOpen' }">
                                        <a class="dropdown-item d-block d-lg-none user-profile-show" >View
                                            profile <i class="ri-user-2-line float-end text-muted"></i></a>
                                        <a class="dropdown-item d-block d-lg-none" @click="videoCreate('audio')"  data-bs-toggle="modal"
                                            data-bs-target="#audiocallModal">Audio <i
                                                class="ri-phone-line float-end text-muted"></i></a>
                                        <a class="dropdown-item d-block d-lg-none" @click="videoCreate()"
                                            data-bs-toggle="modal" data-bs-target="#videocallModal">Video <i
                                                class="ri-vidicon-line float-end text-muted"></i></a>

                                            <a class="dropdown-item" @click="$parent.detailpopup({data:chatHistoryDetails.groupDetail,show:true})">Contact Info</a>
                                            <a class="dropdown-item" v-on:click="ShowSearchMessageArea">Search</a>

                                            <a class="dropdown-item" >Select message</a>
                                            <a class="dropdown-item"  v-on:click="$parent.UsrDeleteHistory(chatHistoryDetails.groupDetail, 'History')" title="Clear Chat">{{ $t('ClearChat') }} </a>





                                        <!-- <a class="dropdown-item" v-on:click="$parent.ArchiveUserGroup(chatHistoryDetails.groupDetail)">{{ chatHistoryDetails.groupDetail.IS_ARCHIVE == true ? $t('UnArchive') : $t('ARCHIVED')}} <i
                                                class="ri-archive-line float-end text-muted"></i></a> -->
                                        <!-- <a class="dropdown-item" >Muted <i
                                                class="ri-volume-mute-line float-end text-muted"></i></a> -->

                                    </div>
                                </div>
                            </li>
                        </ul>
                    </div>
                </div>
            </div>
            <!-- end chat user head -->

            <!-- start chat conversation -->
            <div class="chat-conversation p-3 p-lg-4"
                :class="{ 'chatimg-reply': replyOnData.length > 0 && replyOnData[0].MESSAGE.includes('src'),'chat-reply': replyOnData.length > 0 }"
                data-simplebar="init">
                <ul class="list-unstyled mb-0" id="scrollableDiv">
                    <template v-for="filterByDate in filterSameUser(chatHistoryDetails.resultSet)">
                        <li class="chat_date"><span>{{ getPreviousDay(filterByDate.date) }}</span></li>
                        <li v-for="(item, index) in filterByDate.data" :key="item.CHATLOG_ID"
                            :class="{ 'right': item.FROM_USER_ID == store.GetSenderDetail.id, 'left': item.FROM_USER_ID != store.GetSenderDetail.id,
                            }">
                            <!-- <div class="dividerdate mb-4 mt-4">
                                        <div class="dividerdate_box">{{ getPreviousDay(filterByDate.date,this) }}</div>{{  }}
                                    </div> -->

                            <div class="conversation-list">
                                <div class="chat-avatar"
                                    v-if="item.FROM_USER_ID != store.GetSenderDetail.id && chatHistoryDetails.groupDetail.IS_GROUP">
                                    <img :src="item.AVATAR == blobUserUrl || item.AVATAR == '' || item.AVATAR == null ? defaultImg : item.AVATAR"
                                        alt="">
                                </div>

                                <div class="user-chat-content hover-on-reaction" >
                                    <div class="ctext-wrap" :class="{'filesattachment':(item.ATTACHMENT == 1 && isImageAndVideoFiles(item.FILEEXTENSION) || typeof item.MESSAGE === 'string' &&  item.MESSAGE.includes('src'))}">
                                        <div class="ctext-wrap-content" :id="'chatLog_'+item.CHATLOG_ID" :class="{'blink_message':item.CHATLOG_ID==blinkRepliedText && item.FROM_USER_ID != store.GetSenderDetail.id,'blink_message_blue':item.CHATLOG_ID==blinkRepliedText && item.FROM_USER_ID == store.GetSenderDetail.id}">
                                            <div class="conversation-name yellow-txt"
                                                v-if="item.FROM_USER_ID != store.getUserInformation.ID && item.IS_GROUP == true">
                                                {{ item.USER_NAME }}</div>
                                            <div class="copymessage mb-1" @click="ShowTransitionOnReply(item,'fromReplied')" v-if="item.CHAT_LOG_REF_ID != null && item.CHAT_LOG_REF_ID != 0"
                                                v-html="item.PARENT_MESSAGE"></div>

                                            <div :class="{'d-flex justify-content-between flex-wrap align-items-end': (item.ATTACHMENT != 1 &&   !item.MESSAGE.includes('src')),'attachment':item.MESSAGE == 'application'}">
                                                <div id="reaction_icon">
                                                <div class="mb-0"  v-for="reacted in item.reactedUser">
                                                            <h4 v-html="reacted.ReactionType"></h4>

                                                    </div>
                                                    </div>
                                                <template v-if="item.MESSAGE != 'attachment' && item.ATTACHMENT != 1">
                                                        <div class="mb-0 chat-message" v-html="item.MESSAGE"></div>
                                                    </template>
                                                    <template v-else-if="item.ATTACHMENT == 1">
                                                        <template v-if="item.MESSAGE == 'voice' || item.MESSAGE == 'audio'">
                                                            <audio v-if="item.FILEPATH" :src="item.FILEPATH" controls></audio>
                                                        </template>
                                                        <template v-else-if="item.MESSAGE == 'attachment'">
                                                            <img v-if="isImage(item.FILEEXTENSION)" @click="openLightbox(item.FILEPATH, item.FILEEXTENSION, item.STORAGE_FILE_NAME,item)"
                                                                :src="item.FILEPATH" loading="lazy" alt="image">
                                                        </template>
                                                        <template v-else-if="item.MESSAGE == 'application'">
                                                            <i v-if="isDocument(item.FILEEXTENSION)" @click="openLightbox(item.FILEPATH, item.FILEEXTENSION, item.STORAGE_FILE_NAME,item)" :class="documentIcon(item.FILEEXTENSION)"> </i> <span class="ms-1">{{ item.STORAGE_FILE_NAME }}</span>
                                                        </template>
                                                        <template v-else-if="item.MESSAGE == 'video'">
                                                            <video width="100%" v-if="item.FILEPATH" :src="item.FILEPATH" controls></video>
                                                        </template>
                                                    </template>
                                                <p class="chat-time mb-0">
                                                    <span class="read mt-2"
                                                        v-if="item.FROM_USER_ID == store.GetSenderDetail.id"><i
                                                            class="ri-check-double-line font-size-15"></i></span>
                                                    <span class="align-middle mt-2">
                                                        {{ DateDifference(item.CREATED_AT) == 'Now' ?
                                                    DateDifference(item.CREATED_AT) : getTime(item.CREATED_AT) }}
                                                    </span>
                                                    
                                                </p>
                                            </div>
                                        </div>
                                        <div class="dropdown align-self-center ms-5 hover-out-reaction">
                                            <a  class="fav" v-on:click="reactEmoji(item, null);getPosition($event)" ><svg height="24"
                                                    viewBox="0 0 24 24" width="24" xmlns="http://www.w3.org/2000/svg" ref="reactionEmoji">
                                                    <path
