                                                </svg></a>
                                                <div  v-if="Isrecenttab && reactionToUser.MESSAGE_TOKEN == item.MESSAGE_TOKEN" class="recent-emojis">
                                                        <p ref="selector"><template v-for="emoji in recentEmojis" >
                                                        <span @click="selectEmoji({data:emoji})">{{emoji}}</span></template> 
                                                        <span ref="recentEmojiPicker" @click="OpenEmojiSelector()" >+</span></p>
                        </div>
                                            <a  class="ms-2 reply" v-on:click="ReplyMessageToUser(item)"><svg
                                                    viewBox="0 0 32 32" xmlns="http://www.w3.org/2000/svg">
                                                    <path
                                                        d="m28.88 30a1 1 0 0 1 -.88-.5 15.19 15.19 0 0 0 -13-7.5v6a1 1 0 0 1 -.62.92 1 1 0 0 1 -1.09-.21l-12-12a1 1 0 0 1 0-1.42l12-12a1 1 0 0 1 1.09-.21 1 1 0 0 1 .62.92v6.11a17.19 17.19 0 0 1 15 17 16.34 16.34 0 0 1 -.13 2 1 1 0 0 1 -.79.86zm-14.38-10a17.62 17.62 0 0 1 13.5 6 15.31 15.31 0 0 0 -14.09-14 1 1 0 0 1 -.91-1v-4.59l-9.59 9.59 9.59 9.59v-4.59a1 1 0 0 1 1-1h.54z" />
                                                    <path d="m0 0h32v32h-32z" fill="none" />
                                                </svg></a>
                                            <a class="dropdown-toggle ms-2"  role="button"
                                                data-bs-toggle="dropdown" aria-haspopup="true" aria-expanded="false"
                                                @click="actionMessagePopupOpen(item)">
                                                <i class="ri-more-fill" ref="actionMessageButton"></i>
                                            </a>
                                            <div class="dropdown-menu d-block"
                                                v-if="actionMessagePopupShow == item.CHATLOG_ID"
                                                v-closable="{ exclude: ['actionMessageButton'], handler: 'actionMessagePopup' }">
                                                <button class="dropdown-item"
                                                    v-on:click="copyToClipboard(item)">Copy <i
                                                        class="ri-file-copy-line float-end text-muted"></i></button>
                                                <!-- <a class="dropdown-item" >Save <i class="ri-save-line float-end text-muted"></i></a> -->
                                                <button class="dropdown-item"
                                                    v-on:click="ForwardMessageTo(item)">Forward <i
                                                        class="ri-chat-forward-line float-end text-muted"></i></button>
                                                <button class="dropdown-item"
                                                    v-on:click="DeleteMessage(item, index)">Delete <i
                                                        class="ri-delete-bin-line float-end text-muted"></i></button>
                                                <button class="dropdown-item" v-if="item.ATTACHMENT==1"
                                                    v-on:click="downloadFile(item)">Download <i
                                                        class="ri-download-bin-line float-end text-muted"></i></button>
                                                <!-- <button class="dropdown-item" v-if="item.FROM_USER_ID==store.getUserInformation.ID && formatDateToISO(item.CREATED_AT).split('T')[0] == todayDate"
                                                v-on:click="EditMessage(item, index)">Edit <i
                                                        class="ri-pencil-bin-line float-end text-muted" title="Edit Message"></i></button> -->

                                                <button class="dropdown-item" v-if="item.MESSAGE != 'attachment' && item.ATTACHMENT != 1 && item.MESSAGE != 'voice' && item.ATTACHMENT != 1 && checkIsGIFForEditMessage(item.MESSAGE)"
                                                 v-on:click="$parent.editMessageInfo(item, index)">Message Info <i
                                                        class="ri-pencil-bin-line float-end text-muted" title="Message info"></i></button>

                                            </div>
                                        </div>

                                    </div>



                                </div>


                            </div>

                        </li>

                    </template>
                    <li></li>

                </ul>

            </div>

            <!-- end chat conversation end -->
            <div class="own-message message-bx myself progressbasediv" id="fileProgressId" v-if="fileInProgressShow" ref="progressDiv" tabindex="0">
                        <!-- <div class="control-section" style="
                        float: left;
                        width: 100%;
                        text-align: center;
                    "><div><em class="fa fa-upload"></em> Sending file in process...</div>
                    <progress class="bg-success" id="progressBar" max="100" :value="progressValue" style="width: 100%;"></progress></div> -->
                    <div class="control-section" style=" float: left; width: 100%; text-align: center;">
                        <em class="fa fa-upload"></em> Sending file in process...</div>
                    <div class="progress w-100" >
                    <div class="progress-bar bg-success progress-bar-striped" role="progressbar" :style="'width: '+progressValue +'%'" :aria-valuenow="progressValue" aria-valuemin="0" aria-valuemax="100"></div>
                    </div>
                </div>

                <div id="isShowTyping" v-if="isShowTyping">
                        <div class="conversation-list">


                            <div class="user-chat-content">
                                <div class="ctext-wrap">
                                    <div class="ctext-wrap-content">
                                        <template v-if="isChatOnGroup == true">
                                        <div id="group-is-typing-avatar" class="chat-avatar"  v-for="(itmImg,imgIndex) in isTyping">

                                    <img :src="itmImg == null ? defaultImg : itmImg">
                                </div>
                            </template>

                                        <p class="mb-0">
                                            typing
                                            <span class="animate-typing ps-1">
                                                <span class="dot"></span>
                                                <span class="dot"></span>
                                                <span class="dot"></span>
                                            </span>
                                        </p>
                                    </div>
                                </div>


                            </div>

                        </div>
                    </div>

            <!-- start chat input section -->


            <div class="gif-emoji" v-if="gifShow">
                    <div class="imgGif" v-for="(gifItems,gifIndex) in GifImageData" v-bind:key="gifIndex">
                        <img data-isgif="1" class="gifdata" id="gif0"
                            v-on:click="SetGifToSendArea(gifItems)" :src="gifItems.media[0].gif.url" alt="">
                            <!-- gifItems.media[0].webp_transparent.preview -->
                            <a class="gif_cross d-none" v-on:click="RemoveGifToSend()"><i
                                class="fa fa-close text-danger">
                            </i>
                        </a>
                    </div>
                </div>

            <div class="emoji-section" :class="{ 'chreply': replyOnData.length > 0}">

                <div id="app"  class="emoji-picker-popup" v-closable="{ exclude: ['emojiPicker','reactionEmoji','selector','recentEmojiPicker'], handler: 'emojiClose' }">

<!-- {{ IsreactOnMessage }}demo{{ Isrecenttab }} -->
                    <VEmojiPicker @select="selectEmoji" v-if="IsreactOnMessage"   />
                    <!-- <button v-on:click="closepopup()">close</button>  -->
                </div>
                <div class="chat-input-section p-3 p-lg-3">

                    <p class="copymessage" v-if="isReplytoMessage" v-for="rplyonitm, rplyonitmIndex in replyOnData"
