                        <div v-if="isRecording" class="col">
                        <span class="d-block mt-2"><i class="text-danger fas fa-microphone icon-beat-fade me-1"></i> {{ secondsToHms(recordingTime) }}</span>
                        </div>
                        <div class="col-auto">
                            <div class="chat-input-links ms-md-2 me-md-0">
                                <ul class="list-inline mb-0">
                                    <li class="list-inline-item" v-if="isRecording == false">

                                        <button type="button" v-on:click="GetGifImages()"  class="btn btn-link text-decoration-none font-size-19 btn-lg waves-effect icon-gif">
                                            <Span>GIF</Span>

                                        </button>


                                    </li>



                                    <li class="list-inline-item" data-bs-toggle="tooltip" data-bs-placement="top">
                                        <button type="button"   v-if="isRecording == false" @click="startRecording"
                                            class="btn btn-link text-decoration-none font-size-19 btn-lg waves-effect">
                                            <svg width="24" height="24" stroke-width="1.5" viewBox="0 0 24 24"
                                                fill="none" xmlns="http://www.w3.org/2000/svg">
                                                <rect x="9" y="2" width="6" height="12" rx="3" stroke="currentColor"
                                                    stroke-width="1.5" />
                                                <path
                                                    d="M5 10V11C5 14.866 8.13401 18 12 18V18V18C15.866 18 19 14.866 19 11V10"
                                                    stroke="currentColor" stroke-linecap="round"
                                                    stroke-linejoin="round" />
                                                <path d="M12 18V22M12 22H9M12 22H15" stroke="currentColor"
                                                    stroke-linecap="round" stroke-linejoin="round" />
                                            </svg>

                                        </button>

                                        <button v-if="isRecording" type="button" @click="stopRecording(true)" class="btn btn-link text-danger text-decoration-none font-size-19 btn-lg waves-effect">
                                            <i class="fas fa-trash"></i>

                                 </button>

                                    </li>
                                    <li v-if="isRecording == false" class="list-inline-item" data-bs-toggle="tooltip" data-bs-placement="top">
                                        <button type="button" @click="openFileInput"
                                            class="btn btn-link text-decoration-none font-size-19 btn-lg waves-effect">
                                            <svg width="24" height="24" stroke-width="1.5" viewBox="0 0 24 24"
                                                fill="none" xmlns="http://www.w3.org/2000/svg">
                                                <path
                                                    d="M21.4383 11.6622L12.2483 20.8522C11.1225 21.9781 9.59552 22.6106 8.00334 22.6106C6.41115 22.6106 4.88418 21.9781 3.75834 20.8522C2.63249 19.7264 2 18.1994 2 16.6072C2 15.015 2.63249 13.4881 3.75834 12.3622L12.9483 3.17222C13.6989 2.42166 14.7169 2 15.7783 2C16.8398 2 17.8578 2.42166 18.6083 3.17222C19.3589 3.92279 19.7806 4.94077 19.7806 6.00222C19.7806 7.06368 19.3589 8.08166 18.6083 8.83222L9.40834 18.0222C9.03306 18.3975 8.52406 18.6083 7.99334 18.6083C7.46261 18.6083 6.95362 18.3975 6.57834 18.0222C6.20306 17.6469 5.99222 17.138 5.99222 16.6072C5.99222 16.0765 6.20306 15.5675 6.57834 15.1922L15.0683 6.71222"
                                                    stroke="currentColor" stroke-linecap="round"
                                                    stroke-linejoin="round" />
                                            </svg>

                                        </button>

                                        <input id="up" type="file" style="display: none" @change="setFile($event)" />
                                    </li>
                                    <li class="list-inline-item">
                                        <button type="submit" v-on:click="isRecording ? stopRecording(false) : sendMessage(chatHistoryDetails)"
                                            class="btn btn-primary font-size-16 btn-lg chat-send waves-effect waves-light">
                                            <svg height="20" viewBox="0 0 20 20" width="20"
                                                xmlns="http://www.w3.org/2000/svg">
                                                <path
                                                    d="m2.72112607 2.05149449 15.35446653 7.565969c.247703.12205658.3495596.42180601.227503.66950901-.048698.0988284-.1286747.1788051-.227503.2275031l-15.3541508 7.5658134c-.24770306.1220566-.54745246.0202-.66950904-.227503-.0533719-.1083136-.06574404-.2322825-.03483109-.3490077l1.52125123-5.7446792c.05030971-.1899839.20725125-.3328751.40110728-.3651979l6.88094892-1.1473027c.0842946-.0140491.1539978-.0697032.1874987-.1453514l.018-.0601474c.0194561-.11673645-.0453599-.22804672-.1500414-.27176154l-.0554573-.01593664-6.91980045-1.15330008c-.1939323-.03232205-.35092201-.17529727-.401185-.36537116l-1.48266862-5.60684181c-.07063055-.26695681.08852333-.54062543.35548014-.61125598.11669248-.03087411.24061464-.01849152.3488909.034862z"
                                                    fill="#fff" />
                                            </svg>
                                        </button>
                                    </li>
                                </ul>
                            </div>

                        </div>

                    </div>
                </div>
                <!-- end chat input section -->
            </div>
            <!-- end chat conversation section -->



            <!-- <div class="user-videocall-sidebar">
                        <div class="px-3 px-lg-3 pt-3 pt-lg-3">
                            <div class="video-headerinfo d-flex align-items-center justify-content-between">
