        //filter according to same user image not show
        filterSameUser(data) {
            if (data) {
                var lastIndexName;
                data = data.map(v => ({ ...v, sameName: null }))
                data.map((item, indx) => {
                    if (lastIndexName == item.FROM_USER_ID) {
                        if (indx == data.length - 1) {
                            data[indx].sameName = 1;
                            data[indx - 1].sameName = 0;
                        }
                        else {
                            item.sameName = 0;
                            data[indx - 1].sameName = 0;
                        }
                    }
                    else {
                        if (indx != 0) {
                            if (indx == data.length - 1) {
                                data[indx - 1].sameName = 1;
                                data[indx].sameName = 1;
                            }
                            else {
                                data[indx - 1].sameName = 1;
                                item.sameName = 0;
                            }
                        }
                        else {
                            item.sameName = 1;
                        }
                    }
                    lastIndexName = lastIndexName != item.FROM_USER_ID ? item.FROM_USER_ID : lastIndexName;
                    item.reactedUser = item.reactedUser && typeof item.reactedUser != 'object' ? JSON.parse(item.reactedUser).DATA : item.reactedUser

                    return item;
                })
            }
            // console.log("data", data)

            return data && data.length > 0 ? this.groupBydates(data) : []
        },
