        videoCreate(data) {
            let obj = {
                USER_ID: this.chatHistoryDetails.groupDetail.RECEIVER_ID,
                CONTACTNAME: this.chatHistoryDetails.groupDetail.GROUP_NAME,
                USER_IMAGE: this.chatHistoryDetails.groupDetail.AVATAR,
                GROUP_NAME_ALIAS: this.chatHistoryDetails.groupDetail.GROUP_NAME_ALIAS,
                IS_GROUP: this.chatHistoryDetails.groupDetail.IS_GROUP,
                room: this.chatHistoryDetails.groupDetail.GROUP_ID,
                calltype:data
            }
            this.$parent.videoCreate(obj)
        },
        /*********video call End ******/
    },
    watch: {
    chatHistoryDetails() {
      this.scrollBottom();
    }
  },
    setup() {
        const store = communicationStore()
        const store2 = useloggedUserInfo()
        return {
            store,
            store2,
            getPreviousDay,
            secondsToHms
        }
    }
}

</script>
