<script>
import DataService from '../services/DataService';
import Treeselect from '@riophae/vue-treeselect'
import '@riophae/vue-treeselect/dist/vue-treeselect.css'
import VueEasyLightbox from 'vue-easy-lightbox'
// import 'vue-easy-lightbox/external-css/vue-easy-lightbox.css'

export default {
    components: {
        Treeselect,
        VueEasyLightbox
    },
    props: {
        ProjectTablist:
        {
            type: Array
        },
        ProjectiId: {
            type: String
        }
    },

    data() {
        return {
            Loaddatashow: true,
            MessageDescription: '',
            Messagetitle: '',
            replymessageval: null,
            SelectedProject: '',
            isRemainingToShare: false,
            IsMessageBoardList: true,
            isMessageBoardThreded: false,
            isAddMessagePop: false,
            AttachmentTxtData: [],
            AttachmentDivData: [],
            MessageBoardListData: [],
            projectId: '',
            usertype: '',
            createdBys: '',
            fromDate: '',
            toDate: '',
            searchTxt: '',
            pageSize: 20,
            pageNum: 1,
            sortBy: '',
            sortExp: '',
            reqFrom: 'UC',
            isLoading: false,
            RepliesList: [],
            MessageBoardthreadHeader: '',
            mbCommentTxt: '',
            ProjectUsers: [],
            UsersArray: [],
            MsgBoardid: null,
            isDataLoaded: false,
            isReplyFocus: 'reply-form-without-focus',
            blobUrl: '',
            containerName: '',
            imgPath: '',
            blobUserUrl: '',
            noImageUrl: 'https://talygenv.github.io/Vue-UI-Content/images/DefaultImages/uc_user_new.jpg',
            mbid: 0,
            TeamMembersList: [],
            selectedUsers: null,
            selectAll: false,
            activeIndex: '',
            txtSearchTeamMembers: '',
            TeamErrorMessage: '',
            pageHeight: window.innerHeight,
            pageWidth: window.innerWidth,
            activeMessageboardId: 0,
            MessageboardId: 0,
            imgs: '',  // Img Url , string or Array of string
            visible: false,
            index: 0,  // default: 0
            AttachedFiledata: [],
            filesize: '',
            Filetype: '',
            rows: Array.from({ length: 100 }).map((_, index) => index),
            currentCoords: { top: 0, left: 0 },
            enabled: true
        }
    },
    computed: {
        MessageBoardListData: function () {
            return this.rows.slice(0, this.current)
        },
        computedAttachmentDivData() {
            return this.AttachmentDivData
        },
        updateDimensions() {
            return () => {
                this.pageHeight = window.innerHeight;
                this.pageWidth = window.innerWidth;
            }
        },
    },
    mounted() {

        const listElm = document.querySelector('#div_ProjectMessages');
        if (listElm != null) {
            listElm.addEventListener('scroll', e => {
