        openLightbox: function (imgUrl, path, ext) {
            debugger
            if (ext == '.jpg' || ext == '.png') {
                this.visible = true
                this.index = 0
                this.imgs = [{ title: path.split('/')[3], src: imgUrl }]
            }
            else if (ext == '.pdf') {
                var param = `filePath=${path}`;
                DataService.DownloadAttachment(param)
                    .then(response => {
                        if (response.data != null && response.status === 200) {
                            const byteData = response.data;
                            const fileURL = `data:${ext};base64,${byteData}`;
                            var fileLink = document.createElement('a');
                            fileLink.href = fileURL;
                            fileLink.setAttribute('download', path.split('/')[3]);
                            document.body.appendChild(fileLink);
                            fileLink.click();
                            this.isLoading = false;
                        }
                        else {
                            this.isLoading = false;
                        }
                    });
            }
        },

