                    formData.append("PreMessageType", '');
                    if (vm.AttachmentDivData.length >= 0) {
                        vm.AttachmentDivData.map((item, index) => {

                            var AttachmentFile = item.Attachment;
                            formData.append('File' + index, AttachmentFile);
                        })
                    }
                    DataService.PostMessage(formData).then(async response => {
                        if (!response.data[0]) {
                            debugger;
                            var array = [];
                            vm.AttachmentDivData.forEach(X => {
                                array.push(X.Attachment.name)
                                if (X.Attachment.size > vm.filesize) {
                                    vm.ShowAlert(vm.$t('FileSize', true, [array]), 'warning', true, vm.$t('Alert'));

                                }
                            })
                            return false;
                        }
                        else if (response.data[1].Value == '100') {
                            vm.Messagetitle = '';
                            vm.MessageDescription = '';
                            vm.SelectedProject = '';
                            vm.selectedUsers = null;
                            vm.AttachmentTxtData = [];

                            if (response.data[0].StatusMessage == '1') {
                                //vm.GetAllMessages();
                                vm.ShowAlert(vm.$t("NewMessagePost"), "success", true, vm.$t("Alert"));
                            }
                            else {
                                vm.ShowAlert(vm.$t("MessageEdit"), "success", true, vm.$t("Alert"));
                            }

                        }
                        else if (response.data[1].Value == '121') {
                            vm.Messagetitle = '';
                            vm.MessageDescription = '';
                            vm.SelectedProject = '';
                            vm.selectedUsers = null;
                            vm.AttachmentTxtData = [];

                            vm.CloseAddMessagePop();
                            await vm.GetAllMessages('post');
                            vm.ShowAlert(vm.$t(response.data[1].StatusMessage), "warning", true, vm.$t("Alert"));


                        }
                        //vm.$refs.form.reset();
                        vm.Messagetitle = '';
                        vm.MessageDescription = '';
                        vm.SelectedProject = '';
                        vm.selectedUsers = null;
                        vm.AttachmentTxtData = [];
                        vm.$refs.newMessageForm.reset();
                        await vm.GetAllMessages('post');
                        vm.CloseAddMessagePop();

