                name: e.target.files[0].lastModifiedDate + e.target.files[0].name,
                size: e.target.files[0].size,
                type: e.target.files[0].type,
                messageId: vm.activeMessageboardId,
                file: e.target.files[0]
            })
            var inputName = 'imageUploader' + vm.activeMessageboardId
            // e.target.files[0] = ''
            vm.$refs[inputName].value = null;
            vm.activeMessageboardId = 0
            console.log("chekck", vm.AttachmentTxtData)
            // vm.AttachmentTxtData.txtfileName=e.target.files[0].name
        },
        singleShow(item, id) {
            let obj = item.find(record => record.messageId == id)
            return obj ? true : false
        },
        DeleteMessage: function (mbId, Pid) {

            var vm = this;
            vm.confirmR(vm.$t("ConfirmDeleteMessage"), true, (vm.$t("Delete")), function () {
                var params = `Messageid=${mbId}&projectId=${Pid}&Usertype=${vm.usertype}&reqFrom=${vm.reqFrom}`
                DataService.DeleteMessage(params).then(response => {

                    if (response.data.result[0].StatusCode == 0 && response.data.result[0].MSg == "Message Deleted Successfully") {
                        vm.ShowAlert(vm.$t(response.data.result[0].MSg), "success", true, vm.$t("Alert"));
                    }
                    vm.isAddMessagePop = false;
                    vm.GetAllMessages()
                });
            })

        },
    }
}
</script>
