<template>
    <div class="dialpad-section">
      <div class="px-3 pt-3">
        <h4 class="mb-4 backarrow" @click="$emit('viewType','call')"><i class="fa fa-angle-left"></i> Back </h4>
      </div>
      <div class="px-3 pt-3">
        <div class="dialnumber-dropdwon text-center mb-4">
          <b>Call from: (408) 834-7569 <i class="fa fa-angle-down"></i></b>
        </div>
        <div class="dialinput">
          <input type="tel" class="form-control" placeholder="Enter a name or number" v-model="dialInput"/>
        </div>
        <div class="dialpadbx">
          <div class="dialpadbx-rw">
            <DialPadDigit :digit="1" @input="dialpaidValue"/>
            <DialPadDigit :digit="2" sub="ABC" @input="dialpaidValue"/>
            <DialPadDigit :digit="3" sub="DEF" @input="dialpaidValue"/>
          </div>
          <div class="dialpadbx-rw">
            <DialPadDigit :digit="4" sub="GHI" @input="dialpaidValue"/>
            <DialPadDigit :digit="5" sub="JKL" @input="dialpaidValue"/>
            <DialPadDigit :digit="6" sub="MNO" @input="dialpaidValue"/>
          </div>
          <div class="dialpadbx-rw">
            <DialPadDigit :digit="7" sub="PQRS" @input="dialpaidValue"/>
            <DialPadDigit :digit="8" sub="TUV" @input="dialpaidValue"/>
            <DialPadDigit :digit="9" sub="WXYZ" @input="dialpaidValue"/>
          </div>
          <div class="dialpadbx-rw">
            <DialPadDigit digit="*" @input="dialpaidValue"/>
            <DialPadDigit digit="0" @input="dialpaidValue"/>
            <DialPadDigit digit="#" @input="dialpaidValue"/>
          </div>
          <div class="botrow">
            <div id="call" @click="makeCall"><i class="fas fa-phone-alt"></i></div>
          </div>
        </div>
      </div>
    </div>
  </template>
  
  <script>
  import DialPadDigit from './DialPadDigit.vue';
  
  export default {
    components: {
      DialPadDigit
    },
    data() {
      return {
        dialInput: ''
      };
    },
    methods: {
        dialpaidValue(event){
            this.dialInput += event

        },
      makeCall() {
        // Implement call functionality here
      }
    }
  }
  </script>
  
