<template>
    <div class="accordion-item card mb-2">
      <div class="accordion-header" :id="id + 'info'"  @click="toggle">
        <button 
          class="accordion-button" 
          :class="{'collapsed': !isExpanded}" 
          type="button" 
          data-bs-toggle="collapse" 
          :data-bs-target="'#' + id" 
          :aria-expanded="isExpanded.toString()" 
          :aria-controls="id"
        >
          <h5 class="font-size-14 m-0">{{ title }}</h5>
        </button>
      </div>
      <div 
        :id="id" 
        class="accordion-collapse collapse" 
        :class="{'show': isExpanded}" 
        :aria-labelledby="id + 'info'" 
        data-bs-parent="#settingprofile"
      >
        <slot></slot>
      </div>
    </div>
  </template>
  
  <script>
  export default {
    props: {
      id: {
        type: String,
        required: true
      },
      title: {
        type: String,
        required: true
      },
      expanded: {
        type: Boolean,
        default: false
      }
    },
    data() {
    return {
      isExpanded: this.expanded,
    };
  },
  methods: {
    toggle() {
      this.isExpanded = !this.isExpanded;
    },
  },
  watch: {
    expanded(newVal) {
      this.isExpanded = newVal;
    },
  },
  }
  </script>
  

  