        // get all history of chat user list and history of users
        historyLogUsers(data) {
            var vm = this;
            vm.isDataLoaded = false;
            vm.ChatUserListData = [];
            if (data.resultSet.length > 0) {
                vm.chatUsersList = data.resultSet;
                if (vm.isMobile == true && vm.isMobileNewChat == 1) {
                    vm.isMobileNewChat = 0;
                }
                var arr = [];
                data.resultSet.forEach((message) => {
                    if (!arr.includes(message.RECEIVER_ID)) {
                        vm.ChatUserListData.push(message);
                        if (vm.group <= 0) {
                            if (vm.ReceiverId >= 0) { // if tab is already open and video is accepted
                                vm.openRoomChat(vm.group, vm.sender, null, false);
                            } else {
                                vm.group = message.GROUP_ID;
                                vm.chatUserInfo = message;
                                vm.GroupAliasName = message.GROUP_NAME_ALIAS;
                                vm.userGroupId = message.GROUP_ID;
                                vm.groupNameDisplay = message.GROUP_NAME;
                                vm.openRoomChat(message.GROUP_ID, vm.sender, null, false, message);
                            }
                        }
                        // else{

                        // }
                    }
                });
                vm.store.$state.RecentChatuser=vm.ChatUserListData;
                let all_usersList=vm.ChatUserListData;
                vm.groupRecords = all_usersList.filter(user => user.IS_GROUP && !user.IS_ARCHIVE);
                vm.AarchivedUsersList =all_usersList.filter(user => user.IS_ARCHIVE);
                // Separate pinned users and remove them from vm.ChatUserListData
                vm.pinnedArray = all_usersList.filter(user => user.IS_PINNED && !user.IS_ARCHIVE);
           
                vm.ChatUserListData = all_usersList.filter(user => !user.IS_PINNED && !user.IS_ARCHIVE);
                
                this.pinnedArray.sort((a, b) => new Date(b.MODIFIED_ON) - new Date(a.MODIFIED_ON));

                // Filter records where IS_GROUP is 1 and perform actions with them

            } else {
                vm.groupNameDisplay = '';
                vm.userGroupId = null;
            }
            vm.tmpChatUserList = vm.ChatUserListData;
            vm.tmpAllpinnedArray = vm.pinnedArray;
            vm.tmpArchiveList = vm.AarchivedUsersList;
            vm.tmpgroupRecords=vm.groupRecords;
            this.$socket.emit('onlineUser', this.store.getUserInformation);
