<template>
<div class="div_forDifferentTabs content p-0" id="v-pills-meetings" style="display: block;">
    <ValidationObserver ref="form" v-slot="{ handleSubmit }">
        <form v-on:submit.prevent="handleSubmit(formSubmit)" ref="commentForm">
            <div class="container-fluid p-0">
                <div class="border p-3 bg-white">
                    <div class="row">

                        <div class="col-lg-12 p-0">
                            <div class="col-sm-12 col-md-12 float-left col-lg-6 col-xl-6 p-0">
                                <div class="form-group projectConfig">
                                    <label><span> Chat setting </span>
                                        <span class="text-danger">*</span>
                                        <a href="javascript:;" data-toggle="popoveruserguide" data-content="Enable Approval Receipt Notification">
                                            <i title="Enable Approval Receipt Notification" class="fa fa-question-circle-o text-popover"></i></a></label>
                                    <div type="radiobutton" keys="315" keyname="Approver Receipt Notification" class="rad_value">
                                        <label class="cogfiglbl form-check form-check-inline">
                                            <div class="custom-control custom-radio custom-control-inline">
                                                <input type="radio" name="chatSeetingBox" id="rdo_Right" class="custom-control-input" value="Right" v-model="chatSeetingBox">
                                                <label for="rdo_Right" class="custom-control-label"></label></div>Right
                                        </label>
                                        <label class="cogfiglbl form-check form-check-inline">
                                            <div class="custom-control custom-radio custom-control-inline">
                                                <input type="radio" name="chatSeetingBox" id="rdo_Left" class="custom-control-input" value="Left" v-model="chatSeetingBox">
                                                <label for="rdo_Left" class="custom-control-label"></label></div>Left
                                        </label>
                                        <label class="cogfiglbl form-check form-check-inline">
                                            <div class="custom-control custom-radio custom-control-inline">
                                                <input type="radio" name="chatSeetingBox" id="rdo_default" class="custom-control-input" value="default" v-model="chatSeetingBox">
                                                <label for="rdo_default" class="custom-control-label"></label></div>Default
                                        </label>
                                    </div>
                                </div>
                                <div></div>
                                <div class="col-sm-12 col-md-12 float-left col-lg-12 col-xl-12 p-0"></div>
                            </div>
                        </div>

                    </div>
                    <div class="row flex-row-reverse">
                        <div class="col-lg-6 text-right">
                            <button type="submit" class="btn btn-success" id="btnSave"><i class="fa fa-save pr-2"></i>{{ $t('Save')
                                            }}</button> <button title="" @click="cancel()" class="btn btn-danger" data-toggle-tooltip="tooltip" data-original-title="Cancel"><i class="fa fa-close pr-2"></i>{{ $t('Cancel') }}</button>
                        </div>
                        <div class="col-lg-6">
                            <div class="py-2">
                                <span class="mandatory">{{ $t('MandatoryString') }}</span>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </form>
    </ValidationObserver>

</div>
</template>

<script>
import {
    ValidationObserver
} from 'vee-validate';

import DataService from '../services/DataService';
export default {
    name: "chat Setup",
    components: {
        ValidationObserver,
    },
    props: {
        store: {
            type: Object
        },
    },
    data() {
        return {
            chatSeetingBox: ''
        }
    },
    created() {
        this.FetchData()
    },
    methods: {
        async FetchData() {
            DataService.GetUserChatSetting().then(({data})=>{
                    let jsonData = data ? JSON.parse(data[0].value) : ''
                 var messageData = jsonData ? jsonData[0].layout_type : ''
                this.chatSeetingBox  = messageData ? messageData : 'Default'
            })
        },
        formSubmit() {
           DataService.ChatSetting({layout:this.chatSeetingBox,status_id:1001,User_Id:this.store.getUserInformation.ID}).then(()=>{
            this.ShowAlert(this.$t("saveChatSetting"), "success", true, this.$t("Alert"));
           })
        }

    },

}
</script>
