<template>
    <div class="div_forDifferentTabs content" id="v-pills-meetings" style="display: block;">
        <div class="container-fluid padding-t_10" v-if="!twilioSetup">
            <div class="listing-content">
                <div class="col-lg-12 p-0">
                    <div class="theme-primary partition p-actions-expand">
                        <span class="p-name text-white">{{ $t('TwilioList') }}</span>
                        <span class="p-actions float-right">
                            <a class="p-action-btn text-white" title="" checkprivilege="yes"
                                @click="addTwilioSetup(true)" data-toggle-tooltip="tooltip"
                                data-original-title="Add Twilio"><i class="fa fa-plus"></i><br><span>{{ $t('AddNew')
                                    }}</span>
                            </a>
                        </span>
                    </div>
                </div>
                <div class="listing mt-2">
                    <div class="table-responsive" id="projectdtl">
                        <table id="tblProjectList" class="table table-bordered mt-0">
                            <thead class="thead-bg">
                                <tr>
                                    <th class="sorting ui-resizable" id="th-TITLE"
                                        style="width: 85px; min-width: 85px; max-width: 85px;"><span class="sort"
                                            style="cursor:pointer;"
                                            v-bind:class="{ tb_headerSortUp: SortExp == 'ASC' && SortBy == 'Title', tb_headerSortDown: SortExp == 'DESC' && SortBy == 'Title' }"
                                            v-on:click="sortdata('Title')">{{ $t('Twilio_title') }}</span>
                                        <div id="th-TITLE-sizer" class="sizer"></div>
                                        <div class="ui-resizable-handle ui-resizable-e" style="z-index: 90;"></div>
                                    </th>
                                    <th class="sorting ui-resizable" id="th-ACCOUNTSID"
                                        style="width: 132px; min-width: 132px; max-width: 132px;"><span class="sort"
                                            style="cursor:pointer;"
                                            v-bind:class="{ tb_headerSortUp: SortExp == 'ASC' && SortBy == 'Title', tb_headerSortDown: SortExp == 'DESC' && SortBy == 'Title' }"
                                            v-on:click="sortdata('Title')">{{ $t('AccountSid') }}</span>
                                        <div id="th-ACCOUNTSID-sizer" class="sizer"></div>
                                        <div class="ui-resizable-handle ui-resizable-e" style="z-index: 90;"></div>
                                    </th>
                                    <th class="sorting ui-resizable" id="th-AUTHTOKEN"
                                        style="width: 120px; min-width: 120px; max-width: 120px;"><span class="sort"
                                            style="cursor:pointer;"
                                            v-bind:class="{ tb_headerSortUp: SortExp == 'ASC' && SortBy == 'Title', tb_headerSortDown: SortExp == 'DESC' && SortBy == 'Title' }"
                                            v-on:click="sortdata('Title')">{{ $t('AuthToken') }}</span>
                                        <div id="th-AUTHTOKEN-sizer" class="sizer"></div>
                                        <div class="ui-resizable-handle ui-resizable-e" style="z-index: 90;"></div>
                                    </th>
                                    <th class="text-center sorting ui-resizable" id="th-STATUS"
                                        style="width: 85px; min-width: 85px; max-width: 85px;">{{ $t('Status') }}<div
                                            id="th-STATUS-sizer" class="sizer"></div>
                                        <div class="ui-resizable-handle ui-resizable-e" style="z-index: 90;"></div>
                                    </th>
                                    <th class="text-center sorting ui-resizable" id="th-DELETE"
                                        style="width: 85px; min-width: 85px; max-width: 85px;">{{ $t('Delete') }}<div
                                            id="th-DELETE-sizer" class="sizer"></div>
                                        <div class="ui-resizable-handle ui-resizable-e" style="z-index: 90;"></div>
                                    </th>
                                    <th class="text-center sorting ui-resizable" id="th-DELETE"
                                        style="width: 85px; min-width: 85px; max-width: 85px;">{{ $t('Action') }}<div
                                            id="th-DELETE-sizer" class="sizer"></div>
                                        <div class="ui-resizable-handle ui-resizable-e" style="z-index: 90;"></div>
                                    </th>
                                </tr>
                            </thead>
                            <tbody>
                                <tr v-for="record,index in store.getTwilioList" :key="index">
                                    <td>
                                        <a @click="updateRecord(record)" id="ancEditTwilio" class="text-primary">
                                            {{ record.Title }}
                                        </a>
                                    </td>
                                    <td>
                                        <span>{{ record.AccountSid }}</span>
                                    </td>
                                    <td>
                                        <span>{{ record.AuthToken }}</span>
                                    </td>
                                    <td class="text-center single-action">

                                        <div class="cstm-drop-btn">
                                            <select v-model="record.StatusId" data-id="9" @change="updateStatus(record)"
                                                class="ddlupdatestatus w-80 "
                                                :class="record.StatusId == 1001 ? 'btn-success' : 'btn-danger'">
                                                <option value="1001">Active</option>
                                                <option value="1002">Inactive</option>
                                            </select>
                                        </div>
                                    </td>
                                    <td class="text-center single-action">
                                        <a class="text-danger" @click="deleteRecord(record)" title="" data-twilioid="9"
                                            id="DeleteMultiple" data-toggle-tooltip="tooltip"
                                            data-original-title="Delete"><i class="fa fa-trash"></i></a>
                                    </td>
                                    <td class="text-center single-action">
                                        <a @click="generateSecret(record)" v-if="record.secretValue == null"><i
                                                class="fa fa-key"></i></a>
                                    </td>
                                </tr>
                            </tbody>
                        </table>
                    </div>
                    <Pager v-bind:numberofrecords="TotalRecords" :CurrentPageNumber="CurrentPageNumber" prev-text="Prev"
                        first-text="First" last-text="Last" next-text="Next" v-on:SetCurrentPage="pagerMethod"></Pager>
                </div>
            </div>
        </div>
        <div v-else>
            <section class="page-action">
                <div class="container-fluid">
                    <div class="row">
                        <div class="col-md-12 padding-t_8">
                            <div class="theme-primary partition-full">
                                <span class="p-name text-white">{{ $t('twilioAccount') }}</span>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
            <section class="main-content">
                <ValidationObserver ref="form" v-slot="{ handleSubmit }">
                    <form v-on:submit.prevent="handleSubmit(formSubmit)" ref="commentForm">
                        <div class="container-fluid">
                            <div class="border p-3 bg-white">
                                <div class="row">
                                    <div class="col-lg-6">
                                        <div class="form-group">
                                            <label class="" for="Title">{{ $t('Twilio_title') }}</label>:
                                            <span class="mandatory">*</span>
                                            <ValidationProvider rules="required" v-slot="{ errors }">
                                                <input class="form-control" v-model="deviceSetup.Title" id="Title"
                                                    name="Title" type="text" autocomplete="off"
                                                    :class="errors[0] ? 'is-invalid error' : ''">
                                                <span v-if="errors[0]" class="invalid-feedback">
                                                    {{ $t('ExpTitleRequired') }}
                                                </span>
                                            </ValidationProvider>

                                        </div>
                                    </div>
                                    <div class="col-lg-6">
                                        <div class="form-group">
                                            <label>{{ $t('Status') }}:</label>
                                            <ValidationProvider rules="required" v-slot="{ errors }">
                                                <select class="form-control valid" name="StatusId"
                                                    v-model="deviceSetup.StatusId"
                                                    :class="errors[0] ? 'is-invalid error' : ''">
                                                    <option value="1001">Active</option>
                                                    <option value="1002">Inactive</option>
                                                </select>
                                                <span v-if="errors[0]" class="invalid-feedback">{{ errors[0] }}</span>
                                            </ValidationProvider>

                                        </div>
                                    </div>
                                    <div class="col-lg-6">
                                        <div class="form-group">
                                            <label class="" for="AccountSid">{{ $t('AccountSid') }}</label>:
                                            <span class="mandatory">*</span>
                                            <ValidationProvider rules="required" v-slot="{ errors }">
                                                <input :disabled="update" class="form-control textfield form-control "
                                                    :class="errors[0] ? 'is-invalid error' : ''"
                                                    v-model="deviceSetup.AccountSid" name="AccountSid" type="text">
                                                <span v-if="errors[0]" class="invalid-feedback">
                                                    {{ errors[0] }}
                                                </span>
                                            </ValidationProvider>

                                        </div>
                                    </div>
                                    <div class="col-lg-6">
                                        <div class="form-group">
                                            <label class="" for="AuthToken">{{ $t('AuthToken') }}</label>:<span
                                                class="mandatory">*</span>
                                            <ValidationProvider rules="required" v-slot="{ errors }">
                                                <input :disabled="update" class="form-control textfield form-control"
                                                    :class="errors[0] ? 'is-invalid error' : ''"
                                                    v-model="deviceSetup.AuthToken" maxlength="100" name="AuthToken"
                                                    type="text" autocomplete="off">
                                                <span v-if="errors[0]" class="invalid-feedback">{{ errors[0] }}</span>
                                            </ValidationProvider>
                                        </div>
                                    </div>
                                    <div class="col-lg-12 mt-1">
                                        <div class="form-group">
                                            <label class="" for="Description">{{ $t('description') }}</label>:
                                            <textarea v-model="deviceSetup.Description"
                                                class="form-control form-control" cols="10" id="Description"
                                                maxlength="250" name="Description" rows="5"></textarea>
                                        </div>
                                    </div>
                                </div>
                                <div class="row flex-row-reverse">
                                    <div class="col-lg-6 text-right">
                                        <button type="submit" class="btn btn-success" id="btnSave"><i
                                                class="fa fa-save pr-2"></i>{{ update ? $t('Update') : $t('Save')
                                            }}</button> <button title="" @click="cancel()" class="btn btn-danger"
                                            data-toggle-tooltip="tooltip" data-original-title="Cancel"><i
                                                class="fa fa-close pr-2"></i>{{ $t('Cancel') }}</button>
                                    </div>
                                    <div class="col-lg-6">
                                        <div class="py-2">
                                            <span class="mandatory">{{ $t('MandatoryString') }}</span>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </form>
                </ValidationObserver>
            </section>
            <div class="container-fluid padding-t_10">
                <div class="theme-primary partition-full mt-2">
                    <span class="p-name text-white">{{ $t('ListOfPurchased') }}</span>
                </div>
                <div class="table-responsive">
                    <table id="mainTable1" class="table table-bordered dataTable no-footer">
                        <thead class="thead-bg">
                            <tr>
                                <th scope="col"><span>{{ $t('FriendlyName') }}</span></th>
                                <th scope="col"><span>{{ $t('SelectPhoneNumber') }}</span></th>
                                <th scope="col"><span>{{ $t('IsoCountry') }}</span></th>
                                <th scope="col" class="text-center" name="SMS"><span>{{ $t('SMS') }}</span></th>
                                <th scope="col" class="text-center" name="Voice"><span>{{ $t('Voice') }}</span></th>
                                <th scope="col" class="text-center"><span>{{ $t('Assign') }}</span></th>
                                <!-- <th scope="col" class="text-center" name="Voice"><span>{{ $t('IVR') }}</span></th> -->
                            </tr>
                        </thead>
                        <tbody>
                            <tr v-for="info,numberIndex in numberInfo" :key="numberIndex">
                                <td>{{ info.friendlyName }}</td>
                                <td>{{ info.phoneNumber }}</td>
                                <td>{{ '-' }}</td>
                                <td>{{ info.capabilities ? info.capabilities.sms : '-' }}</td>
                                <td>{{ info.capabilities ? info.capabilities.voice : '-' }}</td>
                                <td><button class="btn btn-success" @click="numberAssignTouserShowPopup(info)">{{  $t('UserAssign') }}</button></td>
                            </tr>
                            <tr v-if="numberInfo.length == 0">
                                <td class="text-center" colspan="6">
                                    <span class="text-danger">{{ $t('No_Record_Found') }}</span>
                                </td>
                            </tr>
                        </tbody>
                    </table>
                </div>
            </div>
        </div>

        <div  class="modal-wrapper modal-transition" style="opacity:1" v-if="userAssignPopup">
            <div class="modal-header">
                <button class="modal-close modal-toggle" v-on:click="userAssignClose()"><i
                        class="fa fa-close"></i></button>
                <h2 class="modal-heading">{{ assignUserInfo.phoneNumber + ' ' + $t('NumberAssignToUser') }} </h2>
            </div>
            <div class="modal-body" style="overflow: auto;">

                <div class="col-12 p-0">
                <div class="col-12 p-0 mt-0 mb-3 float-left">

                    <div class="col-10 float-left pl-0">
                        <div class="float-left w-100">
                            <input id="search" maxlength="50" class="form-control search" type="text" value=""
                                placeholder="Search By Title" autocomplete="off" v-model="userSearch" />
                        </div>
                    </div>
                    <div class="col-2 float-left pl-0">
                        <div class="background-0 text-left form-check-inline">
                            <!-- <a title="" id="Go" href="javascript:;" class="btn btn-success mr-1 px-4" data-toggle-tooltip="tooltip" data-original-title="Search"><i class="fa fa-search"></i></a> -->
                            <a href="javascript:void(0);" @click="userSearch = ''" title=""
                                class="btn btn-danger clearsearchtext px-4" data-toggle-tooltip="tooltip"
                                data-original-title="Clear all "><i class="fa fa-refresh"></i></a>
                        </div>

                    </div>
                </div>

                <div class="col-12 mt-0 mb-4 float-left border p-0" style="max-height: 250px; overflow-y: auto;">
                <div v-for="item,index in userSearchGetData(store.getContactList)"
                    class="modal-content recent-contacts-listing-wrapper border-0" id="checkboxesUser" :key="index">
                    <div class="recent-contacts-listing member p-2">
                        <div class="member-detail">
                            <p>
                                <input type="checkbox" class="chkTeamMembers" v-model="users" :value="item.USER_ID" />
                                {{ item.CONTACTNAME }}
                            </p>
                        </div>
                    </div>
                </div>
            </div>
                </div>

          




                

            </div>
            <div class="modal-footer">
                <a href="javascript:;" class="btn btn-success" v-on:click="assignUser()"> {{ $t('Assign') }}</a>
            </div>
        </div>
    </div>
</template>

<script>
import {
    ValidationProvider,
 
    ValidationObserver
} from 'vee-validate';
import DataService from '../services/DataService';
import Pager from '@/components/Common/Pager.vue';
export default {
    name: "Setup",
    components: {
        ValidationProvider,
        ValidationObserver,
        Pager
    },
    props: {
        store: {
            type: Object
        },
    },
    data() {
        return {
            deviceSetup: {
                Title: '',
                StatusId: '',
                AccountSid: '',
                AuthToken: '',
                Description: '',
            },
            numberInfo: [],
            twilioSetup: false,
            TotalRecords: 0,
            CurrentPageNumber: 0,
            PageNumber: 1,
            PageSize: 10,
            SortBy: '',
            SortExp: '',
            PageSizeOption: null,
            update: false,
            userAssignPopup: false,
            assignUserInfo: '',
            users: [],
            userSearch: ''
        }
    },
    created() {
        this.FetchData()
        //  this.$socket.emit('twilioSendMessage', {from:'+12513095602',to:'+918209463215' },function (record) {
        //             console.log("createApiKey",record)

        //     })
        let url = `search=&pageSize=&pageNum=&sortBy=&sortExp=&type=company&city=&departmentId=`;
        this.store.getContact(url)


    },
    methods: {
        async FetchData() {
            var vueObj = this;
            let url = `pageSize=${vueObj.PageSize}&pageNum=${vueObj.PageNumber}&sortBy=${vueObj.SortBy}&sortExp=${vueObj.SortExp}`
            await vueObj.store.twilioList(url)
            vueObj.TotalRecords = this.store.getTwilioList.length > 0 ? vueObj.store.getTwilioList[0].TotalRecord : 0
        },
        sortdata: function (sortBy) {

            this.SortBy = sortBy;
            var sortExp = 'ASC';
            if (this.SortExp == 'ASC') {
                sortExp = 'DESC';
            }
            this.SortExp = sortExp;
            this.PageNumber = 1;
            this.FetchData();
        },
        addTwilioSetup(e) {
            this.twilioSetup = e
            this.update = false
        },
        cancel() {
            this.twilioSetup = false
        },
        formSubmit() {
            var vm = this;
            vm.$socket.emit('validTwilioAccount',{ACCOUNT_SID:vm.deviceSetup.AccountSid,AUTH_TOKEN:vm.deviceSetup.AuthToken}, function (record) {
                if(record.status==true){
                    vm.$socket.emit('createApiKey', { deviceSetup: vm.deviceSetup }, function (record) {
                        vm.deviceSetup.secretValue = record.sid
                        vm.deviceSetup.accountKey =record.secret
            DataService.AddTwilio(vm.deviceSetup).then((record) => {
                console.log("record", record.data)
                if (record.data) {
                    if (record.data[0].StatusMessage == 'UpdatedSuccess' || record.data[0].StatusMessage == 'UpdatedSuccess') {
                        vm.update = false
                        let url = `pageSize=${vm.PageSize}&pageNum=${vm.PageNumber}&sortBy=${vm.SortBy}&sortExp=${vm.SortExp}`
                        vm.store.twilioList(url)
                        vm.ShowAlert(vm.$t(record.data[0].StatusMessage, { 0: vm.$t('twilioAccount') }), "success", true, vm.$t("Alert"));
                    } else {
                        vm.ShowAlert(vm.$t(record.data[0].StatusMessage), "warning", true, vm.$t("Alert"));
                    }
                } else {
                    vm.ShowAlert(vm.$t("Error"), "warning", true, vm.$t("Alert"));
                }

                vm.$socket.emit('buyNumberList', {
                    deviceSetup: vm.deviceSetup
                }, function (record) {
                    vm.numberInfo = record
                })
            })
        })
        }else{
            vm.ShowAlert(vm.$t("TwilioAccountWrong"), "warning", true, vm.$t("Alert"));
        }
    })
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },
        updateStatus(record) {
            console.log("record", record)
            DataService.AddTwilio(record)
        },
        updateRecord(record) {
            var vm = this;
            vm.twilioSetup = true
            vm.update = true
            vm.numberInfo = []
            Object.assign(this.deviceSetup, record)
            vm.$socket.emit('buyNumberList', {
                deviceSetup: vm.deviceSetup
            }, function (record) {
                console.log("testttt", record)
                vm.numberInfo = record
            })
        },
        deleteRecord(record) {
            var vm = this;
            vm.confirmR(vm.$t("ConfirmDeleteAccount"), true, (vm.$t("Delete") + "  " + +vm.$t("Records")), async function () {
                DataService.DeleteTwilio(`id=${record.TwilioCredentialId}`).then((result) => {
                    if (result.data) {
                        vm.store.$state.twilioListaState = vm.store.getTwilioList.filter((item) => item.TwilioCredentialId !== record.TwilioCredentialId)
                        vm.ShowAlert(vm.$t("TwilioDelete"), "success", true, vm.$t("Alert"));
                    } else {
                        vm.ShowAlert(vm.$t("Error"), "warning", true, vm.$t("Alert"));
                    }
                })
            })
        },
        generateSecret(itm) {
            var vm = this;
            vm.$socket.emit('createApiKey', { deviceSetup: itm }, function (record) {
                var obj = {
                    twilioCredentialId: itm.TwilioCredentialId,
                    key: record.sid,
                    secret: record.secret,
                }
                console.log("createApiKey", record)
                //vm.numberInfo = record
                DataService.AssignAccountKeyAndSecret(obj).then((result) => {
                    console.log("checkk", result.data)
                })
            })
        },
        numberAssignTouserShowPopup(itm) {
            this.assignUserInfo = itm
            console.log("numberAssignToUser", itm)
            this.userAssignPopup = true

        },
        assignUser() {
            // console.log("testt",this.assignUserInfo)
            // console.log("this.deviceSetup",this.deviceSetup)
            let obj = {
                "extension": this.assignUserInfo.phoneNumber,
                "twilioCredentialId": this.deviceSetup.TwilioCredentialId,
                "status_id": '1001',
                "config_id": '',
                "name": '',
                "to_User_Id": ''

            }
            this.users.map((item, index) => {
               let objectName =  this.store.getContactList.find(data=>  data.USER_ID==item)
               obj.name += index == 0 ? objectName.CONTACTNAME : ',' + objectName.CONTACTNAME
                obj.to_User_Id += index == 0 ? item : ',' + item
            })
            DataService.AssignNumberToUser(obj).then((result) => {
                this.users = []
                this.userAssignPopup = false
                this.userSearch = ''
                this.store.GetTwilioUserConfigDetail()
                if(result.data && result.data[0].value=="1"){
                this.ShowAlert(this.$t('NumberAssign'), "success", true, this.$t("Alert"));
                }else{
                    this.ShowAlert(this.$t('SomeThingWent'), "danger", true, this.$t("Alert"));
                }
            })

        },
        userSearchGetData(data) {
            if (this.userSearch) {
                var results = [];
                results = data.filter(obj => obj.CONTACTNAME.toLowerCase().includes(this.userSearch.toLowerCase()));
                return this.userSearch != '' ? results : data
            }
            else {
                return data
            }
        },
        userAssignClose(){
            this.users = []
            this.userAssignPopup = false
            this.userSearch = ''
        }

    },

}
</script>
