<template>
    <div class="digit" @click="addDigit">
      {{ digit }}
      <div v-if="sub" class="sub">{{ sub }}</div>
    </div>
  </template>
  
  <script>
  export default {
    props: {
      digit: {
        type: [String, Number],
        required: true
      },
      sub: {
        type: String,
        default: ''
      }
    },
    methods: {
      addDigit() {
        this.$emit('input', this.digit);
      }
    }
  }
  </script>
  
 
  